import React, {FC, useState} from "react";
import {SourcesList} from "./SourcesList";
import {SourceDataConnectionForm} from "../ServiceConnection/Forms/Sources/SourceDataConnectionForm";
import {CurrentProjectAtom} from "../../atoms/project-atoms/CurrentProjectAtom";
import {useRecoilState} from "recoil";
import {ImportDataSetsFromSourceNameAtom} from "../../atoms/ModalAtoms";

export const SourcesPage: FC = () => {
    const [project,] = useRecoilState(CurrentProjectAtom);
    const [isAddingNewSource, setIsAddingNewSource] = useState(false);
    const [editingSourceName, setEditingSourceName] = useState("");
    const [, setImportingFromSourceName] = useRecoilState(ImportDataSetsFromSourceNameAtom);
    const connectionToEdit = () => project?.connections?.filter(x => x.sourceName === editingSourceName)[0] ?? null;

    return isAddingNewSource || editingSourceName != "" ? <SourceDataConnectionForm
            existing={connectionToEdit()} setIsAddingNewSource={setIsAddingNewSource}
            setEditingSourceName={setEditingSourceName}/> :
        <SourcesList
            setImportingSourceName={setImportingFromSourceName}
            setEditingSourceName={setEditingSourceName} setIsAddingNewSource={setIsAddingNewSource}/>
}