import {Sheet} from "@mui/joy";
import Box from "@mui/joy/Box";
import React, {FC, useState} from "react";
import {useRecoilState} from "recoil";
import {serviceType} from "../../api/enums/serviceType";
import Project from "../../api/project";
import {IsNewProjectDialogOpenAtom, IsOpenProjectDialogOpenAtom} from "../../atoms/ModalAtoms";
import {CurrentProjectAtom} from "../../atoms/project-atoms/CurrentProjectAtom";
import {
    PersistedStorageConnectionForm
} from "../ServiceConnection/Forms/PersistedStorage/PersistedStorageConnectionForm";
import {ServiceTypeSelectionForm} from "../ServiceConnection/Forms/ServiceTypeSelectionForm";
import {StyledButton, StyledStorageConnectForm} from "../utility/StyledComponentStyles";
import { LastUsedAWSRegion } from "../../atoms/LastUsedAWSRegionAtom";

export const WelcomeAndSetup: FC = () => {
    const [project, setProject] = useRecoilState(CurrentProjectAtom);
    const [isStorageConnected, setIsStorageConnected] = useState<boolean>(false);

    const [, setOpenNewProjectDialog] = useRecoilState(IsNewProjectDialogOpenAtom);
    const [, setOpenOpenProjectDialog] = useRecoilState(IsOpenProjectDialogOpenAtom);
    const [, setRegion] = useRecoilState(LastUsedAWSRegion);

    const updateService = (s: serviceType) => {
        setProject({...project, projectStore: {...project?.projectStore, serviceType: s}} as Project);
        setIsStorageConnected(false);
        if (s === serviceType.FILE_S3 && project?.projectStore.serverAddress)
        {
            setRegion(project!.projectStore.serverAddress);
        }
    };


    return (<Box sx={{
        height: "100vh",
        m: 0,
        p: 0,
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        backgroundColor: "#032047",
    }}>
        <Sheet
            sx={{
                mx: "auto",
                p: .6,
                display: "flex",
                flexDirection: "column",
                gap: 3,
                borderRadius: "md",
                boxShadow: "none",
                backgroundImage: "linear-gradient(to top right, #12A5DD, #5E4AE3)"
            }}
        >
            <Box
                sx={{
                    width: 660,
                    p: 4,
                    display: "flex",
                    flexDirection: "column",
                    gap: 3,
                    borderRadius: "sm",
                    backgroundColor: "#032047",
                }}
            >
                <img
                    alt="Versor Logo"
                    src="metadata-manager-logo.png"
                    className="logo-versor"
                    style={{
                        maxWidth: "280px", margin: "0 auto 8px"
                    }}
                />
                <StyledStorageConnectForm>

                    <ServiceTypeSelectionForm setService={updateService}
                                              selectedService={project?.projectStore.serviceType ?? null} isProject/>
                    <br/>
                    {project?.projectStore.serviceType &&
                        <PersistedStorageConnectionForm existingSource={project.projectStore}
                        updateSource={(x) => setProject({
                            ...project, projectStore: { ...x}
                                                        } as Project)}
                                                        selectedServiceType={project.projectStore.serviceType}
                                                        setIsConnected={setIsStorageConnected}/>}

                </StyledStorageConnectForm>
                {isStorageConnected && <Box>
                    <StyledButton size="lg" onClick={() => setOpenNewProjectDialog(true)} fullWidth>New
                        Project</StyledButton>
                    <br/><br/>
                    <StyledButton size="lg" onClick={() => setOpenOpenProjectDialog(true)} fullWidth>Open
                        Project</StyledButton>
                </Box>}
            </Box>
        </Sheet>
    </Box>);
};