import {Button, CircularProgress, FormControl, FormLabel, Input, Stack, Typography} from "@mui/joy";
import {Popover} from "@mui/material";
import {styled} from "@mui/material/styles";
import React, {FC, useEffect} from "react";
import {SourceApiClient} from "../../../api-client/SourceApiClient";
import {dataSetType} from "../../../api/enums/dataSetType";
import projectSource from "../../../api/projectSource";
import sourceDataset from "../../../api/sourceDataset";

const Popup = styled(Popover)({
    zIndex: 10000000000,
});

export interface FileBrowserPopupProps {
    dataset: sourceDataset | null;
    popoverAnchorEl: HTMLElement | null;
    setPopoverAnchorEL: (el: HTMLElement | null) => void;
    source?: projectSource;
    updateDataset: (ds: sourceDataset) => void;
}

export const FileBrowserPopup: FC<FileBrowserPopupProps> = (props) => {
    const popoverOpen = Boolean(props.popoverAnchorEl);

    const [tested, setTested] = React.useState(false);
    const [testSuccess, setTestSuccess] = React.useState<boolean | undefined>(undefined);
    const [columns, setColumns] = React.useState<string[]>([]);
    const [isTesting, setIsTesting] = React.useState(false);
    const [tempParserOptions, setTempParserOptions] = React.useState(props.dataset?.parserOptions);

    const handleTest = () => {
        setIsTesting(true);
        setTested(false);
        const client = new SourceApiClient();
        const tempDS: sourceDataset = {...props.dataset!, parserOptions: tempParserOptions!};
        const tempSource: projectSource = {...props.source, datasets: [tempDS]} as projectSource;
        client.ListDatasets(tempSource).then(x => {
            console.log(x);
            if (x.success && x.data.datasets[0]?.schema.length > 0) {
                setColumns(x.data.datasets[0].schema.map(x => x.name));
                props.updateDataset(x.data.datasets[0]);
            }
            setTestSuccess(x.success);
            setIsTesting(false);
            setTested(true);
        });

    }

    useEffect(() => {
        if (popoverOpen) {
            setTested(false);
            setTestSuccess(undefined);
            setColumns([]);
            setTempParserOptions(props.dataset?.parserOptions);
        }
    }, [popoverOpen]);

    return <Popup
        sx={{borderRadius: 2}}
        open={popoverOpen}
        anchorEl={props.popoverAnchorEl}
        anchorOrigin={{
            vertical: 'center',
            horizontal: 'right',
        }}
        transformOrigin={{
            vertical: 'center',
            horizontal: 'right',
        }}
        onClose={() => props.setPopoverAnchorEL(null)}
    >
        <Stack direction={"column"} spacing={1} overflow={"auto"} flexWrap={"wrap"}
               flexGrow={1} padding={2}>
            {props.dataset?.name != null &&
                <Typography level={"h6"}>{props.dataset?.name.split('/').slice(-1)}</Typography>}
            {props.dataset?.type && [dataSetType.CSV, dataSetType.TEXT].includes(props.dataset?.type) &&
                <React.Fragment>
                    <FormControl size={"sm"}>
                        <FormLabel>Header Row</FormLabel>
                        <Input type={"number"}
                               value={tempParserOptions?.headerRowStartsAt ?? 0}
                               onChange={(e) =>
                                   setTempParserOptions((x) =>
                                       ({...x!, headerRowStartsAt: Number.parseInt(e.target.value)}))}/>
                    </FormControl>
                    <FormControl size={"sm"}>
                        <FormLabel>Data Row</FormLabel>
                        <Input type={"number"}
                               value={tempParserOptions?.dataRowStartsAt ?? 1}
                               onChange={(e) =>
                                   setTempParserOptions((x) =>
                                       ({...x!, dataRowStartsAt: Number.parseInt(e.target.value)}))}/>
                    </FormControl>
                    <FormControl size={"sm"}>
                        <FormLabel>Row Delimiter</FormLabel>
                        <Input type={"text"}
                               value={tempParserOptions?.newLineDelimiter ?? '\n'}
                               onChange={(e) =>
                                   setTempParserOptions((x) =>
                                       ({...x!, newLineDelimiter: e.target.value}))}/>
                    </FormControl>
                    {props.dataset?.type === dataSetType.CSV && (
                        <FormControl size={"sm"}>
                            <FormLabel>Col Delimiter</FormLabel>
                            <Input type={"text"}
                                   value={tempParserOptions?.csv?.delimiter ?? ','}
                                   onChange={(e) =>
                                       setTempParserOptions((x) =>
                                           ({...x!, csv: {...x?.csv, delimiter: e.target.value}}))}/>
                        </FormControl>
                    )}
                </React.Fragment>}
            <Button size={"sm"} variant={"outlined"} disabled={isTesting} onClick={handleTest}>{isTesting ?
                <CircularProgress size={"sm"}/> : "Test"}</Button>
            {tested && testSuccess && <Typography>Columns: {columns.length}</Typography>}
            {tested && !testSuccess && !isTesting && < Typography> Parse Failed</Typography>}
        </Stack>
    </Popup>
}