import {Box, Button, CircularProgress, Modal, ModalDialog, Stack, Typography} from "@mui/joy";
import React, {FC, useEffect, useState} from "react";
import {useRecoilState} from "recoil";
import {SourceApiClient} from "../../api-client/SourceApiClient";
import {IsServiceTypeDatabase} from "../../api-extensions/ServiceTypeExtensions";
import Project from "../../api/project";
import projectSource from "../../api/projectSource";
import sourceDataset from "../../api/sourceDataset";
import {AvailableLoadTypesAtom} from "../../atoms/AvailableLoadTypesAtom";
import {ImportDataSetsFromSourceNameAtom} from "../../atoms/ModalAtoms";
import {CurrentProjectAtom} from "../../atoms/project-atoms/CurrentProjectAtom";
import {GetServiceGroupNameForServiceType, ServiceGroupChip} from "../ServiceConnection/Forms/ServiceTypeSelectionForm";
import {DatasetTransferList} from "../Sources/DatasetTransferList";
import {FileBrowserList} from "../Sources/FileBrowser/FileBrowserList";

export const ImportDatasetsModal: FC = () => {
    const [project, setProject] = useRecoilState(CurrentProjectAtom);
    const [importingFromSourceName, setImportingFromSourceName] = useRecoilState(ImportDataSetsFromSourceNameAtom);
    const [importingSource, setImportingSource] = useState<projectSource | null>(null);
    const [isLoading, setIsLoading] = useState(false);
    const [isOpen, setIsOpen] = useState(false);
    const [remoteDatasets, setRemoteDatasets] = useState<sourceDataset[]>([]);
    const [newDatasets, setNewDatasets] = useState<sourceDataset[]>([]);
    const [isSaving, setIsSaving] = useState(false);
    const [error, setError] = useState<string>("");
    const [loadTypes,] = useRecoilState(AvailableLoadTypesAtom);

    const GetRemoteEntities = async () => {
        const sourceClient = new SourceApiClient();
        const result = await sourceClient.ListEntities(importingSource!);
        if (result.success) {
            setRemoteDatasets(result.data);
        } else {
            setError(result.errors[0])
        }
        setIsLoading(false);
    }
    const closeModal = () => {
        setIsOpen(false);
        setImportingFromSourceName("");
        setImportingSource(null);
    };
    const Save = () => {
        setIsSaving(true);
        let updatedSource: projectSource = {...importingSource, datasets: newDatasets} as projectSource;
        const emptyDatasets = newDatasets.filter(x => x.schema == null || x.schema.length < 1) ?? [];
        if (emptyDatasets.length > 0) {
            const tempSource = {...importingSource, datasets: emptyDatasets} as projectSource;

            const client = new SourceApiClient();
            client.ListDatasets(tempSource)
                .then(x => {
                    if (x.success) {
                        console.log(x);
                        let datasetsToUse = newDatasets.map(y => x.data.datasets.find(z => z.name.toLowerCase() === y.name.toLowerCase()) ?? y)
                        datasetsToUse = datasetsToUse.map(x => {
                            return {...x, loadType: loadTypes[0]}
                        })

                        updatedSource = {...updatedSource, datasets: datasetsToUse};

                        const updatedConnections: projectSource[] = project?.connections?.map(x => x.sourceName === updatedSource.sourceName ? updatedSource : x) as projectSource[];
                        const updatedProject: Project = {...project, connections: updatedConnections} as Project;
                        setProject(updatedProject)
                    } else {
                        alert(x.errors[0]);
                    }
                });
        }
        const updatedConnections: projectSource[] = project?.connections?.map(x => x.sourceName === updatedSource.sourceName ? updatedSource : x) as projectSource[];
        const updatedProject: Project = {...project, connections: updatedConnections} as Project;
        setProject(updatedProject)

        setIsSaving(false);
        closeModal();
    }

    useEffect(() => {
        if (importingFromSourceName === "") {
            closeModal();
        } else {
            setError("")
            setIsOpen(true);
            setRemoteDatasets([]);
            setIsLoading(true);
            const src = project?.connections?.filter(x => x.sourceName === importingFromSourceName)[0] ?? null;
            if (src != null) setImportingSource(src);
        }
    }, [importingFromSourceName]);
    useEffect(() => {
        if (importingSource != null) {
            GetRemoteEntities();
        }
    }, [importingSource]);

    return (<Modal open={isOpen} onClose={closeModal}>
        <ModalDialog size={"lg"} aria-labelledby="basic-modal-dialog-title"
                     aria-describedby="basic-modal-dialog-description">
            <Typography id="basic-modal-dialog-title" level={"h3"}>
                Import Datasets from <Typography
                fontStyle={"italic"}
                level={"h2"}>
                {importingSource?.sourceName} {importingSource?.sourceConnection.serviceType && ServiceGroupChip(GetServiceGroupNameForServiceType(importingSource!.sourceConnection.serviceType))}
            </Typography>
            </Typography>

            <Typography id="basic-modal-dialog-description" textColor="text.tertiary">
                {isLoading &&
                    <CircularProgress size={"sm"}/>}
            </Typography>
            {remoteDatasets?.length > 0 && importingSource != null &&
                <Box>
                    {IsServiceTypeDatabase(importingSource.sourceConnection.serviceType)
                        ?
                        <DatasetTransferList source={importingSource}
                                             left={remoteDatasets}
                                             right={importingSource?.datasets ?? []}
                                             setDatasetsToImport={setNewDatasets}
                        />
                        :
                        <FileBrowserList source={importingSource} left={remoteDatasets}
                                         right={importingSource?.datasets ?? []}
                                         setDatasetsToImport={setNewDatasets}/>
                    }

                </Box>}
            {error !== "" && <Typography color={"danger"}>{error}</Typography>}
            <Stack direction="row" justifyContent={"space-between"} display={"flex"} paddingTop={1}>
                <Button disabled={isSaving} variant={"outlined"} onClick={closeModal}>Cancel</Button>
                <Button disabled={isSaving} onClick={Save}>Save{isSaving && <CircularProgress/>}</Button>
            </Stack>
        </ModalDialog>
    </Modal>);
}
