import {Input, Option, Select, Stack, Typography} from "@mui/joy";
import {FC} from "react";
import {useRecoilState} from "recoil";
import sourceDataset from "../../api/sourceDataset";
import {AvailableLoadTypesAtom} from "../../atoms/AvailableLoadTypesAtom";
import {EntityToOpenInEditor} from "../../atoms/EntityToOpenInEditorAtom";
import {StyledButton} from "../utility/StyledComponentStyles";

export interface EditorHeaderProps {
    changesSaved: boolean,
    Save: () => void,
    fieldSearchFilter: string,
    setFieldSearchFilter: (value: string) => void,
    RevertChanges: () => void,
    tempDataset: sourceDataset,
    setTempDataset: (s: sourceDataset) => void,
    entityToOpenInEditor: EntityToOpenInEditor,
}

export const EditorHeader: FC<EditorHeaderProps> = (props) => {
    const [loadTypes,] = useRecoilState(AvailableLoadTypesAtom)
    const {
        changesSaved,
        Save,
        fieldSearchFilter,
        setFieldSearchFilter,
        RevertChanges,
        tempDataset,
        setTempDataset,
        entityToOpenInEditor
    } = props;

    return (
        <Stack spacing={2} display={"flex"}>
            <Stack direction={"row"} spacing={5}>
                <StyledButton
                    disabled={changesSaved}
                    onClick={Save}
                >
                    Save Changes
                </StyledButton>
                <Input
                    size={"md"}
                    fullWidth
                    placeholder={"Search Fields..."}
                    value={fieldSearchFilter}
                    onChange={(e) => setFieldSearchFilter(e.target.value.toLowerCase())}/>
                <StyledButton
                    disabled={changesSaved}
                    color={"danger"}
                    onClick={RevertChanges}
                >
                    Revert Changes
                </StyledButton>
            </Stack>
            <Stack direction={"row"} justifyContent={"space-between"}>
                <Typography><b>Source Name</b><br/>{entityToOpenInEditor?.sourceName}</Typography>
                <Typography><b>Dataset Name</b><br/>{entityToOpenInEditor?.datasetName}</Typography>
                <Typography><b>Dataset Type</b><br/>{tempDataset?.type}</Typography>
                <Stack>
                    <Typography><b>Dataset Load Type</b></Typography>
                    <Select size={"sm"} onChange={(e, v) =>
                        setTempDataset({...tempDataset, loadType: v!})
                    }
                            value={(tempDataset?.loadType)}>
                        {loadTypes.map(
                            (key) =>
                                <Option key={'loadType.' + key}
                                        value={key}>{key}</Option>
                        )}
                    </Select>
                </Stack>
            </Stack>
        </Stack>
    )
}