export const toTitleCase = (str: string): string => {
    return str.replace(/\w\S*/g, (txt) => {
        return txt.charAt(0).toUpperCase() + txt.substring(1).toLowerCase();
    });
};

export const dimOnTrue = (flag:boolean) => {
    return {
        opacity: flag ? 0.15 : 1,
    }
}

export const disableOnTrue = (flag:boolean) => {
    return {
        pointerEvents: flag ? 'none' : 'initial'
    }
}

export const ShouldDisableComponent = (shouldDisable:boolean) => {
    return { ...disableOnTrue(shouldDisable), ...dimOnTrue(shouldDisable) }
}