import React, {FC, useState} from "react";
import {useRecoilState} from "recoil";
import {CurrentProjectAtom} from "../../../../atoms/project-atoms/CurrentProjectAtom";
import {ServiceTypeSelectionForm} from "../ServiceTypeSelectionForm";
import {serviceType} from "../../../../api/enums/serviceType";
import {ServiceConnectionFormLayout} from "../ServiceConnectionFormLayout";
import {ServiceConnectionServerAddressField} from "../FormFields/ServiceConnectionServerAddressField";
import {ServiceConnectionContainerNameField} from "../FormFields/ServiceConnectionContainerNameField";
import {ServiceConnectionUsernameField} from "../FormFields/ServiceConnectionUsernameField";
import {ServiceConnectionPasswordField} from "../FormFields/ServiceConnectionPasswordField";
import projectSource from "../../../../api/projectSource";
import ProjectSource from "../../../../api/projectSource";
import Project from "../../../../api/project";
import serviceConnectionDetail from "../../../../api/serviceConnectionDetail";
import {FormControl, FormLabel, Input, Stack} from "@mui/joy";
import { LastUsedAWSRegion } from "../../../../atoms/LastUsedAWSRegionAtom";

export interface SourceDataConnectionFormProps {
    existing?: projectSource | null;
    setIsAddingNewSource?: (isAddingNewSource: boolean) => void;
    setEditingSourceName?: (editingSourceName: string) => void;
}

export const SourceDataConnectionForm: FC<SourceDataConnectionFormProps> = (props) => {
    const [project, setProject] = useRecoilState(CurrentProjectAtom);
    const [typeOfService, setTypeOfService] = useState<serviceType | null>(props.existing?.sourceConnection.serviceType ?? null);

    const [source, setSource] = useState<ProjectSource>(props.existing ?? {} as projectSource);
    const [,setRegion] = useRecoilState(LastUsedAWSRegion);

    const updateSourceConnectionDetails = (connectionDetails: serviceConnectionDetail) => {
        setSource({ ...source, sourceConnection: connectionDetails });
        if (connectionDetails.serviceType === serviceType.FILE_S3 && connectionDetails.serverAddress)
        {
            setRegion(connectionDetails.serverAddress);
        }
    }

    const updateService = (service: serviceType) => {
        setTypeOfService(service);
        setSource({...source, sourceConnection: {...source.sourceConnection, serviceType: service}});
    }
    const saveSource = (): any => {
        const newConnections = props.existing ? project!.connections.map(x => x.sourceName === source.sourceName ? source : x) : [...project?.connections ?? [], source];
        setProject({
            ...project,
            connections: newConnections
        } as Project);
        cancelAndExit();
    };
    const cancelAndExit = (): any => {
        props.setIsAddingNewSource?.(false);
        props.setEditingSourceName?.("");
    };
    const deleteSource = (): any => {
        const excludedConnections = project?.connections?.filter(x => x.sourceName !== source?.sourceName) ?? [];
        const updatedProject = {...project, connections: excludedConnections} as Project;
        setProject(updatedProject);
        cancelAndExit();
    };

    return <Stack>
        <ServiceTypeSelectionForm setService={updateService}
                                  selectedService={typeOfService} isProject={false}/>
        <br/>
        {typeOfService &&
            <React.Fragment>
                <FormControl required={true} disabled={!!props.existing}>
                    <FormLabel>Source Name</FormLabel>
                    <Input type="text" value={source.sourceName}
                           onChange={e => setSource({...source, sourceName: e.target.value})}/>
                </FormControl>
                <br/>
                <ServiceConnectionFormLayout isEditing={props.existing != null && props.existing != undefined}
                                             isProject={false}
                                             connection={source.sourceConnection} save={saveSource}
                                             cancel={cancelAndExit}
                                             delete={deleteSource}>
                    <ServiceConnectionServerAddressField updateConnectionDetails={updateSourceConnectionDetails}
                                                         existing={source.sourceConnection}/>
                    <ServiceConnectionContainerNameField updateConnectionDetails={updateSourceConnectionDetails}
                                                         existing={source.sourceConnection}/>
                    <ServiceConnectionUsernameField updateConnectionDetails={updateSourceConnectionDetails}
                                                    existing={source.sourceConnection}/>
                    <ServiceConnectionPasswordField updateConnectionDetails={updateSourceConnectionDetails}
                                                    existing={source.sourceConnection}/>
                </ServiceConnectionFormLayout>
            </React.Fragment>
        }
    </Stack>
};
