import {Box, Button, Checkbox, Typography} from "@mui/joy";
import React, {FC} from "react";
import {TreeItem, treeItemClasses, TreeItemProps} from "@mui/lab";
import {SvgIconProps} from "@mui/material";
import {styled} from "@mui/material/styles";
import sourceDataset from "../../../api/sourceDataset";
import {ArrowRight, FolderOpen} from "@mui/icons-material";

interface FileBrowserTreeNodeEndProps {
    ds: sourceDataset;
    handlePopoverOpen: (event: any, ds: any) => void;
}

const FileBrowserTreeNodeEnd: FC<FileBrowserTreeNodeEndProps> = (props) => {
    return <Button size={'sm'} variant={'plain'} color={'info'} sx={{marginRight: 2}}
                   onClick={(event) => props.handlePopoverOpen(event, props.ds)}>Options <ArrowRight/></Button>
}

export const FileBrowserTreeNode = (availableDatasets: sourceDataset[], currentObj: any, key: string, previousKey: string, indent: number, checkedDatasetNames: string[], setCheckedDatasetNames: (names: string[]) => void, handlePopoverOpen: (event: any, ds: any) => void) => {
    const fullKey = previousKey + "/" + key;
    const fileExtension = key.split('.').slice(-1)[0].toLowerCase();
    const validFileExtensions = ["avro", "csv", "txt", "parquet", "xml", "json"];
    const validFileExtensionsThatNeedOptions = ["csv", "txt", "xml", "json"];
    const IsChecked = (name: string): boolean => checkedDatasetNames.includes(name);
    const UpdateChecked = (name: string, checked: boolean): void => {
        if (checked) {
            setCheckedDatasetNames([...checkedDatasetNames, name]);
        } else {
            setCheckedDatasetNames(checkedDatasetNames.filter(x => x !== name));
        }
    }
    if (validFileExtensions.includes(fileExtension)) {
        const slicedFK = fullKey.slice(1);
        const ds = availableDatasets.find(x => x.name === slicedFK)

        if (!ds) return;
        return <StyledTreeItem key={fullKey}
            nodeId={fullKey}
            checkboxNode={ds.schema.length > 0 && <Checkbox sx={{padding: 1 }} key={fullKey + '.check'}
                                                       checked={IsChecked(ds!.name)}
                                                       onChange={(e) => UpdateChecked(ds!.name, e.target.checked)}
                                                       />}
                               indent={indent}
                               labelText={key}
                               EndNode={validFileExtensionsThatNeedOptions.includes(fileExtension) &&
                                   <FileBrowserTreeNodeEnd ds={ds} handlePopoverOpen={handlePopoverOpen}/>}/>
    } else {
        return <StyledTreeItem key={fullKey} nodeId={fullKey} labelText={key}
                               labelIcon={FolderOpen} indent={indent}>
            {Object.keys(currentObj).map((k) => {
                return FileBrowserTreeNode(availableDatasets, currentObj[k], k, fullKey, indent + 1, checkedDatasetNames, setCheckedDatasetNames, handlePopoverOpen);
            })}
        </StyledTreeItem>
    }
}

export type StyledTreeItemProps = TreeItemProps & {
    bgColor?: string;
    color?: string;
    checkboxNode?: any;
    labelIcon?: React.ElementType<SvgIconProps>;
    EndNode?: React.ReactNode;
    labelText: string;
    indent?: number;
};

export const StyledTreeItemRoot = styled(TreeItem)(({theme}) => ({
    color: theme.palette.text.secondary,
    [`& .${treeItemClasses.content}`]: {
        color: theme.palette.text.secondary,
        borderTopRightRadius: theme.spacing(2),
        borderBottomRightRadius: theme.spacing(2),
        paddingRight: theme.spacing(1),
        fontWeight: theme.typography.h5,
        '&.Mui-expanded': {
            fontWeight: theme.typography.h6,
        },
        '&:hover': {
            backgroundColor: theme.palette.action.hover,
        },
        '&.Mui-focused, &.Mui-selected, &.Mui-selected.Mui-focused': {
            backgroundColor: `var(--tree-view-bg-color, ${theme.palette.action.selected})`,
            color: 'var(--tree-view-color)',
        },
        [`& .${treeItemClasses.label}`]: {
            fontWeight: 'inherit',
            color: 'inherit',
        },
    },
    [`& .${treeItemClasses.group}`]: {
        marginLeft: 0,
        [`& .${treeItemClasses.content}`]: {
            paddingLeft: theme.spacing(2),
        },
    },
}));

export function StyledTreeItem(props: StyledTreeItemProps) {
    const {
        bgColor,
        color,
        checkboxNode,
        labelIcon: LabelIcon,
        EndNode,
        labelText,
        indent = 0,
        ...other
    } = props;

    return (
        <StyledTreeItemRoot
            label={
                <Box sx={{display: 'flex', alignItems: 'center', p: 0.5, pr: 0, paddingLeft: 2 * (props.indent ?? 0)}}>
                    {checkboxNode}
                    {props.labelIcon &&
                        <Box component={LabelIcon} color="inherit" sx={{mr: 1}}/>}
                    <Typography level="body2" sx={{fontWeight: 'inherit', flexGrow: 1}}>
                        {labelText}
                    </Typography>
                    {EndNode}
                </Box>
            }
            {...other}
        />
    );
}