import React, {FC, useEffect, useMemo, useState} from "react";
import {
    Button,
    Card,
    Checkbox,
    Divider,
    FormControl,
    Grid,
    Input,
    List,
    ListDivider,
    ListItemButton,
    ListItemDecorator,
    Typography
} from "@mui/joy";
import {CardHeader} from "@mui/material";
import sourceDataset from "../../api/sourceDataset";
import {UniqueArray} from "../utility/HelperFunctions";
import projectSource from "../../api/projectSource";

export interface DatasetTransferListProps {
    left: sourceDataset[];
    right: sourceDataset[];
    setDatasetsToImport: (datasets: sourceDataset[]) => void;
    source?: projectSource;
}

export const sortDataSet = (x: sourceDataset, y: sourceDataset) => {
    if (x.name < y.name) {
        return 1;
    }
    if (x.name > y.name) {
        return -1;
    }
    return 0;
}

export const DatasetTransferList: FC<DatasetTransferListProps> = (props) => {
    const [searchText, setSearchText] = useState<string>("");

    const [availableDatasets, setAvailableDatasets] = useState<sourceDataset[]>(props.left.filter(x => !props.right.map(y => y.name).includes(x.name)));
    const [importedDatasets, setImportedDatasets] = useState<sourceDataset[]>(props.right);

    const displayAvailableDatasets = useMemo(() => availableDatasets.filter(x => x.name.toLowerCase().includes(searchText.toLowerCase())).sort((a, b) => a.name.localeCompare(b.name)), [availableDatasets, searchText]);
    const displayImportedDatasets = useMemo(() => importedDatasets.filter(x => x.name.toLowerCase().includes(searchText.toLowerCase())).sort((a, b) => a.name.localeCompare(b.name)), [importedDatasets, searchText]);

    const [checkedDatasetNames, setCheckedDatasetNames] = useState<string[]>([]);

    const toggleSetDatasetChecked = (datasetName: string) => {
        if (checkedDatasetNames.includes(datasetName)) {
            setCheckedDatasetNames(checkedDatasetNames.filter(x => x !== datasetName));
        } else {
            setCheckedDatasetNames([...checkedDatasetNames, datasetName]);
        }
    }

    const toggleAvailableToImported = () => {
        const dsToMove = availableDatasets.filter(x => checkedDatasetNames.includes(x.name));
        setImportedDatasets(UniqueArray([...importedDatasets, ...dsToMove]));
        setAvailableDatasets(UniqueArray(availableDatasets.filter(x => !dsToMove.includes(x))));
        setCheckedDatasetNames(UniqueArray(checkedDatasetNames.filter(x => !dsToMove.map(y => y.name).includes(x))));
    }
    const toggleImportedToAvailable = () => {
        const dsToMove = importedDatasets.filter(x => checkedDatasetNames.includes(x.name));
        setAvailableDatasets(UniqueArray([...availableDatasets, ...dsToMove]));
        setImportedDatasets(UniqueArray(importedDatasets.filter(x => !dsToMove.includes(x))));
        setCheckedDatasetNames(UniqueArray(checkedDatasetNames.filter(x => !dsToMove.map(y => y.name).includes(x))));
    }
    const searchTextChanged = (event: React.ChangeEvent<HTMLInputElement>) => {
        setSearchText(event.target.value);
    }

    useEffect(() => {
        console.log(importedDatasets);
        props.setDatasetsToImport(importedDatasets);
    }, [importedDatasets]);

    const checkedDatasetNamesContainsAll = (names: string[]) => {
        for (const n of names) {
            if (!checkedDatasetNames.includes(n)) return false;
        }
        return true;
    }
    const checkedDatasetNamesContainsSome = (names: string[]) => {
        for (const n of names) {
            if (checkedDatasetNames.includes(n)) return true;
        }
        return false;
    }
    const setCheckDatasetsInSchema = (names:string[]) =>
    {
        if (checkedDatasetNamesContainsAll(names)) {
            setCheckedDatasetNames(checkedDatasetNames.filter(x=> !names.includes(x)));
        } else 
            setCheckedDatasetNames(Array.from(new Set([...checkedDatasetNames, ...names])));

    }


    const customList = (title: React.ReactNode, items: readonly sourceDataset[]) => {
        const schemas = Array.from(new Set(items.map(x => x.name.split('/')[0])));
        const mappedItems = schemas.map(x => { return {
                key: x,
                value: 
                    items.filter(i => i.name.split('/')[0] === x)
            }
        });

        return (
        <Card variant={"outlined"}>
            <CardHeader
                sx={{ px: 2, py: 1 }}
                title={title}
                subheader={`${items.length} Items`}/>
            <Divider/>
            <List
                sx={{
                    width: 400,
                    height: 530,
                    bgcolor: 'background.paper',
                    overflow: 'auto',
                }}
                component="div"
                role="list">
                {mappedItems.map(x => {
                        const parentLabelId = `transfer-list-all-item-${x.key}-label`;
                    return (
                            <React.Fragment>
                                <ListItemButton
                                    key={x.key}
                                    role="listitem"
                                onClick={() => setCheckDatasetsInSchema(x.value.map(y=> y.name))}>
                                    <ListItemDecorator>
                                        <Checkbox
                                        indeterminate={checkedDatasetNamesContainsSome(x.value.map(y => y.name)) && !checkedDatasetNamesContainsAll(x.value.map(y => y.name))}
                                            checked={checkedDatasetNamesContainsAll(x.value.map(y=> y.name))}/>
                                    </ListItemDecorator>
                                    <Typography id={parentLabelId}>{x.key}</Typography>
                                </ListItemButton>
                                {x.value.map((ds) => {
                                const labelId = `transfer-list-all-item-${ds.name}-label`;

                                return (
                                    <ListItemButton
                                        key={ds.name}
                                        role="listitem"
                                        onClick={() => toggleSetDatasetChecked(ds.name)}
                                        sx={{ paddingLeft: 4 }}>
                                        <ListItemDecorator>
                                            <Checkbox
                                                checked={checkedDatasetNames.includes(ds.name)}
                                                tabIndex={-1} />
                                        </ListItemDecorator>
                                        <Typography id={labelId}>{ds.name}</Typography>
                                    </ListItemButton>
                                );
                            })}
                            </React.Fragment>
                            )
                        }
                    )}
            </List>
        </Card>
    )};

    return (
        <Grid container spacing={2} justifyContent="center" alignItems="center">
            <Grid xs={12}>
                <FormControl>
                    <Input type={"text"} placeholder="Search Datasets" fullWidth onChange={searchTextChanged}
                           value={searchText}/>
                </FormControl>
            </Grid>
            <Grid>{customList('Available Datasets', displayAvailableDatasets)}</Grid>
            <Grid>
                <Grid container direction="column" alignItems="center">
                    <Button
                        sx={{my: 0.5}}
                        variant="outlined"
                        size="sm"
                        onClick={toggleAvailableToImported}
                        aria-label="move selected right"
                    >
                        &gt;
                    </Button>
                    <Button
                        sx={{my: 0.5}}
                        variant="outlined"
                        size="sm"
                        onClick={toggleImportedToAvailable}
                        aria-label="move selected left"
                    >
                        &lt;
                    </Button>
                </Grid>
            </Grid>
            <Grid>{customList('Imported Datasets', displayImportedDatasets)}</Grid>
        </Grid>
    );
}