import { DeleteForever, WarningRounded } from "@mui/icons-material";
import { Box, Button, Divider, Modal, ModalDialog, Typography } from "@mui/joy";
import { FC, useEffect, useState } from "react";
import { useRecoilState } from "recoil";
import { IsAlertUnsavedChangesDialogOpenAtom } from "../../atoms/ModalAtoms";

export interface AlertUnsavedChangesDialogProps {
    newDatasetName: string,
    continueOpen: () => void
};
export const AlertUnsavedChangesDialog: FC<AlertUnsavedChangesDialogProps> = (props) => {
    const [open, setOpen] = useRecoilState(IsAlertUnsavedChangesDialogOpenAtom);


    return (
            <Modal open={open} onClose={() => setOpen(false)}>
                <ModalDialog
                    variant="outlined"
                    role="alertdialog"
                    aria-labelledby="alert-dialog-modal-title"
                    aria-describedby="alert-dialog-modal-description"
                >
                    <Typography
                        id="alert-dialog-modal-title"
                        component="h2"
                        startDecorator={<WarningRounded />}>
                        Unsaved Changes
                    </Typography>
                    <Divider />
                    <Typography id="alert-dialog-modal-description" textColor="text.tertiary">
                        There are unsaved changes to the currently open dataset. Are you sure you want to open <b>{props.newDatasetName}</b>?
                    </Typography>
                    <Box sx={{ display: 'flex', gap: 1, justifyContent: 'flex-end', pt: 2 }}>
                        <Button variant="plain" color="neutral" onClick={() => setOpen(false)}>
                            Cancel
                        </Button>
                        <Button variant="solid" color="danger" onClick={() => { setOpen(false); props.continueOpen(); }}>
                            Discard Changes
                        </Button>
                    </Box>
                </ModalDialog>
            </Modal>
    );
}