import React, {FC} from "react";
import {Box, Chip, Grid, List, ListItem, ListItemContent, Stack, Typography} from "@mui/joy";
import {StyledButton} from "../utility/StyledComponentStyles";
import {CurrentProjectAtom} from "../../atoms/project-atoms/CurrentProjectAtom";
import {useRecoilState} from "recoil";
import {GetServiceGroupNameForServiceType, ServiceGroupChip} from "../ServiceConnection/Forms/ServiceTypeSelectionForm";

export interface SourcesListProps {
    setEditingSourceName: (name: string) => void;
    setImportingSourceName: (name: string) => void;
    setIsAddingNewSource: (isAdding: boolean) => void;
}

export const SourcesList: FC<SourcesListProps> = (props) => {
    const [project,] = useRecoilState(CurrentProjectAtom);
    const getSources = () => project?.connections ?? [];
    return <Stack display={"flex"} width={"100%"} flexGrow={1} padding={1}>
        {getSources().length < 1
            ? <Box sx={{display: "flex", alignItems: "center", justifyContent: "center"}}>
                <Typography fontWeight={600}>Start by adding a new linked source</Typography>
            </Box>
            : <List>
                {getSources().map((x) =>
                    <ListItem key={x.sourceName} sx={{p: 1}}>
                        <ListItemContent>
                            <Grid container xs={12} display={"flex"} spacing={0}>
                                <Grid xs={3} display={"flex"}>
                                    {ServiceGroupChip(GetServiceGroupNameForServiceType(x.sourceConnection.serviceType))}
                                </Grid>
                                <Grid xs={5} display={"flex"} wrap={"wrap"}
                                      sx={{wordWrap: 'break-word', wordBreak: 'break-all', lineBreak: 'auto'}}>
                                    <Typography flexWrap={"wrap"}
                                                justifyContent={"left"}>{x.sourceName} {!x.sourceConnection.password &&
                                        <Chip size={"sm"} color={"danger"}>Missing Password!</Chip>}</Typography>
                                </Grid>
                                <Grid xs={3} display={"flex"}>
                                    <Stack direction={"row"} spacing={0.5}>
                                        <StyledButton variant="plain" style={{marginRight: "2px"}} size={"sm"}
                                                      onClick={() => props.setEditingSourceName(x.sourceName)}>Edit</StyledButton>
                                        <StyledButton disabled={!x.sourceConnection.password} variant="solid"
                                                      size={"sm"}
                                                      onClick={() => props.setImportingSourceName(x.sourceName)}>Import
                                            Datasets</StyledButton>
                                    </Stack>
                                </Grid>
                            </Grid>
                        </ListItemContent>
                    </ListItem>)}
            </List>}
        <StyledButton size={"lg"} fullWidth onClick={() => props.setIsAddingNewSource(true)}>New
            Linked Source</StyledButton>
    </Stack>
}
