import {FormControl, FormLabel, Grid, Input} from "@mui/joy";
import React, {FC} from "react";
import {serviceType} from "../../../../api/enums/serviceType";
import serviceConnectionDetail from "../../../../api/serviceConnectionDetail";
import {ServiceConnectionFieldProps} from "./ServiceConnectionFieldProps";

export const ServiceConnectionUsernameField: FC<ServiceConnectionFieldProps> = (props) => {
    const servicesThatExcludeUsername = [serviceType.FILE_AZURE_BLOB];

    return !servicesThatExcludeUsername.includes(props.existing!.serviceType) ? <Grid xs={12}>
        <FormControl required={true}>
            <FormLabel>{GetLabelForServiceType(props.existing!.serviceType)}</FormLabel>
            <Input fullWidth={true}
                   name={"username"}
                   value={props.existing?.username}
                   onChange={(x) => props.updateConnectionDetails({
                       ...props.existing, username: x.target.value
                   } as serviceConnectionDetail)}/>
        </FormControl>
    </Grid> : <React.Fragment/>;
};

const GetLabelForServiceType = (s: serviceType): string =>
{
    switch (s)
    {
        case serviceType.FILE_S3:
            return "Access Key";
        default:
            return "Username";
    }
};