import {ExpandLess, ExpandMore} from "@mui/icons-material";
import {
    Button,
    Card,
    FormControl,
    FormLabel,
    Grid,
    Input,
    List,
    ListItem,
    ListItemContent,
    Option,
    Select,
    Stack,
    Typography
} from "@mui/joy";
import {Collapse} from "@mui/material";
import React, {FC, useEffect, useRef} from "react";
import {databricksColumnType} from "../../api/enums/databricksColumnType";
import sourceDataset from "../../api/sourceDataset";
import sourceDataSetField from "../../api/sourceDataSetField";
import {ShouldDisableComponent} from "../../Utility";
import {BooleanChipAttribute} from "./EditorBooleanChip";

export interface RenderRowProps {
    UpdateVal: (field: sourceDataSetField, val: any, attributeName: string) => void,
    index: number,
    style: any,
    filteredSchemas: sourceDataSetField[],
    tempDataset: sourceDataset,
    setTempDataset: (val: sourceDataset) => void,
    showAdvanced: boolean[],
    UpdateShowAdvanced: (show: boolean, index: number) => void,
    listRef: any,
    rowHeights: any;
    availableClassifications: string[];
}

export const RenderRow: FC<RenderRowProps> = (props) => {
    const {
        index,
        style,
        filteredSchemas,
        UpdateVal,
        setTempDataset,
        tempDataset,
        showAdvanced,
        UpdateShowAdvanced,
        listRef,
        rowHeights
    } = props;
    const rowRef = useRef<any>({});
    const f = filteredSchemas![index];
    const k = f.name + '.field';

    function setRowHeight(index: number, size: any) {
        listRef.current.resetAfterIndex(0);
        rowHeights.current = {...rowHeights.current, [index]: size};
    }

    useEffect(() => {
        if (rowRef.current) {
            setRowHeight(index, rowRef.current.clientHeight);
        }
    }, [rowRef]);

    const shouldDisable = IsDisabled(f.source.type);


    return <ListItem key={k} style={style}>
        <ListItemContent>
            <Card ref={rowRef} sx={ShouldDisableComponent(shouldDisable)}>
                <Grid container spacing={2} display={"flex"}>
                    <Grid xs={2}>
                        <Stack>
                            <Typography>Field Name</Typography>
                            <Typography sx={{wordWrap: "break-word"}}
                                        level={"h5"}>{f.name}</Typography>
                        </Stack>
                    </Grid>
                    <Grid xs={3}>
                        <Stack spacing={1} direction={"column"}>
                            <FormControl>
                                <FormLabel>Source</FormLabel>
                                <Typography>{f.source.type}</Typography>
                            </FormControl>
                            {!shouldDisable && <Stack direction={"row"} spacing={1}>
                                {GetDatabricksSelectBox(f, UpdateVal)}
                                {IsPrecisionApplicableForSource(f.source.type) &&
                                    <React.Fragment>
                                        <FormControl size={"sm"}>
                                            <FormLabel>Precision</FormLabel>
                                            <Input sx={{width: '50px'}}
                                                   value={f.destination.precision?.toString()}
                                                   type={"range(0,38)"}
                                                   onChange={(e) => UpdateVal(f, Number.parseInt(e.target.value), 'precision')}/>
                                        </FormControl>
                                        <FormControl size={"sm"}>
                                            <FormLabel>Scale</FormLabel>
                                            <Input sx={{width: '50px'}}
                                                   value={f.destination.scale?.toString()}
                                                   type={"range(0,38)"}
                                                   onChange={(e) => UpdateVal(f, Number.parseInt(e.target.value), 'scale')}/>
                                        </FormControl>
                                    </React.Fragment>}
                            </Stack>}
                        </Stack>
                    </Grid>
                    {!shouldDisable &&
                        <React.Fragment>
                            <Grid xs={5}>
                                <Stack>
                                    <Typography>Attributes</Typography>
                                    <Stack direction={"row"} flexWrap={"wrap"}>
                                        {BooleanChipAttribute(f, "isIdentity", "Identity", tempDataset!, setTempDataset)}
                                        {BooleanChipAttribute(f, "isNullable", "Nullable", tempDataset!, setTempDataset)}
                                        {BooleanChipAttribute(f, "isBatchIdentity", "Batch Identity", tempDataset!, setTempDataset)}
                                        {BooleanChipAttribute(f, "isWatermark", "Watermark", tempDataset!, setTempDataset)}
                                        {BooleanChipAttribute(f, "isHashed", "Hash", tempDataset!, setTempDataset)}
                                        {BooleanChipAttribute(f, "exclude", "Exclude from Silver", tempDataset!, setTempDataset)}
                                    </Stack>
                                </Stack>
                            </Grid>
                            <Grid xs={2}>
                                <Stack direction={"row"} spacing={1}>
                                    <Stack>
                                        <Typography>Classification</Typography>
                                        <Select size={"sm"}
                                                onChange={(e, v) => UpdateVal(f, v!, 'classification')}
                                                value={(f.classification)}>
                                            {props.availableClassifications.map(
                                                (key) => {
                                                    return <Option
                                                        key={f.name + '.classification.' + key}
                                                        value={key}>{key}</Option>;
                                                })}
                                        </Select>
                                    </Stack>
                                    <Button size={"sm"} variant={"plain"} sx={{height: '20px'}}
                                            onClick={() => UpdateShowAdvanced(!(showAdvanced[index]), index)}>
                                        {showAdvanced[index] ? <ExpandMore/> : <ExpandLess/>}
                                    </Button>
                                </Stack>
                            </Grid>
                            <Collapse in={showAdvanced[index]}>
                                <Stack direction={"row"} spacing={2} marginTop={2}
                                       justifyContent={"space-between"}>
                                    <FormControl size={"sm"}>
                                        <FormLabel>Redact Expression</FormLabel>
                                        <Input fullWidth
                                               value={f.redactExpression ?? ""}
                                               onChange={(e) => UpdateVal(f, e.target.value, 'redactExpression')}/>

                                    </FormControl>hot

                                    <FormControl size={"sm"}>
                                        <FormLabel>Custom Notebook Function</FormLabel>
                                        <Input fullWidth
                                               value={f.customProcessingFunction ?? ""}
                                               onChange={(e) => UpdateVal(f, e.target.value, 'customProcessingFunction')}/>
                                    </FormControl>
                                    <FormControl size={"sm"}
                                                 disabled={!IsTimezoneApplicableForSource(f.destination.type)}>
                                        <FormLabel>Timezone
                                            Override {!(IsTimezoneApplicableForSource(f.destination.type)) && "(Not Applicable)"}</FormLabel>
                                        <Input fullWidth value={f.timezoneOverride ?? ""}
                                               onChange={(e) => UpdateVal(f, e.target.value, 'timezoneOverride')}/>
                                    </FormControl>
                                </Stack>
                            </Collapse>
                        </React.Fragment>}
                </Grid>
            </Card>
        </ListItemContent>
    </ListItem>;
};


const IsTimezoneApplicableForSource = (srcType: string) => ["DATETIME"].includes(srcType.toUpperCase());
const IsPrecisionApplicableForSource = (srcType: string): boolean => ["DECIMAL", "FLOAT", "DOUBLE"].includes(srcType.toUpperCase());
const IsDisabled = (sourceType: string) => ["object", "array"].includes(sourceType.toLowerCase());
const GetDatabricksSelectBox = (field: sourceDataSetField, UpdateVal: (field: sourceDataSetField, val: any, attributeName: string) => void) => {
    const itemsList = [
        {key: "Whole Number", value: ["BIGINT", "INT", "SMALLINT", "TINYINT"]},
        {key: "Part Number", value: ["DECIMAL", "FLOAT", "DOUBLE"]},
        {key: "Date & Time", value: ["DATE", "TIMESTAMP"]},
        {key: "Simple", value: ["BOOLEAN", "STRING", "BINARY"]},

    ];

    return (
        <FormControl size={"sm"}>
            <FormLabel>Destination</FormLabel>
            <Select
                value={field.destination.type}
                onChange={(e, v) =>
                    UpdateVal(field, v as databricksColumnType, 'destination.type')
                }
                slotProps={{
                    listbox: {
                        sx: {
                            maxHeight: 300,
                            overflow: 'auto'
                        }
                    }
                }}>
                <List>
                    {itemsList.map(h => (
                        <React.Fragment key={"listItem." + h.key.replace(' ', '')}>
                            <List>
                                <ListItem sticky>
                                    <Typography level="body2" textTransform="uppercase" letterSpacing="md">
                                        {h.key}
                                    </Typography>
                                </ListItem>
                                {h.value.map(v => (
                                    <Option key={"destination." + v} value={v}>{v}</Option>
                                ))}
                            </List>
                        </React.Fragment>
                    ))}
                </List>
            </Select>
        </FormControl>
    );
};