import { Check } from "@mui/icons-material";
import { Checkbox, Chip } from "@mui/joy";
import sourceDataset from "../../api/sourceDataset";
import sourceDataSetField from "../../api/sourceDataSetField";

export const BooleanChipAttribute = (f: sourceDataSetField, attributeName: string, label: string, tempDataset: sourceDataset, setTempDataset: (x: sourceDataset) => void) => {
    const boolAttr: boolean = f[attributeName as keyof typeof f] as boolean;
    return (
        <Chip size={"sm"}
            key={f.name + '.' + attributeName}
            variant={boolAttr ? 'soft' : 'plain'}
            color={boolAttr ? 'primary' : 'neutral'}
            startDecorator={
                boolAttr && <Check sx={{ zIndex: 1, pointerEvents: 'none' }} />
            }
        >
            <Checkbox size={"sm"}
                variant="outlined"
                color={boolAttr ? 'primary' : 'neutral'}
                disableIcon
                overlay
                label={label}
                checked={boolAttr}
                onChange={(event) => {
                    const newField = { ...f, [attributeName]: event.target.checked };
                    const updatedFields = tempDataset?.schema.map((x) => x.name === f.name ? newField : x);
                    setTempDataset({ ...tempDataset, schema: updatedFields! } as sourceDataset);
                }}
            />
        </Chip>
    )
}