import React, {FC, ForwardRefExoticComponent, useState} from "react";
import {
    Alert,
    Button,
    List,
    ListDivider,
    ListItem,
    ListItemButton,
    Menu,
    MenuItem,
    menuItemClasses,
    Stack,
    Typography,
    typographyClasses
} from "@mui/joy";
import {useRecoilState} from "recoil";
import {CurrentProjectAtom} from "../../atoms/project-atoms/CurrentProjectAtom";
import {AlertSnackbarAtom, AlertSnackbarContentAtom} from "../Notification/AlertSnackbar";
import {
    IsCompareProjectDialogOpenAtom,
    IsNewProjectDialogOpenAtom,
    IsOpenProjectDialogOpenAtom,
    IsSaveProjectDialogOpenAtom
} from "../../atoms/ModalAtoms";
import {ProjectApiClient} from "../../api-client/ProjectApiClient";

export const WorkspaceMenu: FC = () => {
    const menus = React.useRef<HTMLElement[]>([]);
    const [menuIndex, setMenuIndex] = useState<number | null>(null);
    const [project,] = useRecoilState(CurrentProjectAtom);
    const [, setOpenProjectDialogAtom] = useRecoilState(IsOpenProjectDialogOpenAtom);
    const [, setNewProjectDialogAtom] = useRecoilState(IsNewProjectDialogOpenAtom);
    const [, setSaveProjectDialogAtom] = useRecoilState(IsSaveProjectDialogOpenAtom);
    const [, setAlertOpen] = useRecoilState(AlertSnackbarAtom);
    const [, setAlertContent] = useRecoilState(AlertSnackbarContentAtom);
    const [, setCompareOpen] = useRecoilState(IsCompareProjectDialogOpenAtom);

    const OpenProject = () => setOpenProjectDialogAtom(true);
    const SaveProject = () => {
        setSaveProjectDialogAtom(true);
    };
    const NewProject = () => CloseProject();
    const CloseProject = () => {
        window.localStorage.removeItem("recoil-persist");
        window.location.reload();
    }
    const CompareProject = () => setCompareOpen(true);
    const PublishMetadata = () => {
        const client = new ProjectApiClient();
        client.ExportProject(project!).then((response) => {
            if (response.status === 200) {
                // create file link in browser's memory
                const href = URL.createObjectURL(response.data);

                // create "a" HTML element with href to file & click
                const link = document.createElement('a');
                link.href = href;
                link.setAttribute('download', 'MetadataExport.json'); //or any other extension
                document.body.appendChild(link);
                link.click();

                // clean up "a" element & remove ObjectURL
                document.body.removeChild(link);
                URL.revokeObjectURL(href);
            } else {
                console.log(response.data[0])
                setAlertContent(<Alert color={"danger"}>Export Failed!</Alert>);
            }
            setAlertOpen(true);
        })
    }
    const ShowAbout = () => {
        alert("not implemented yet");
    }
    const ReportIssue = () => {
        window?.open("https://forms.office.com/Pages/ResponsePage.aspx?id=CRm1Wk13DUSK6yJJeFfJMDt92CYyKV9MkC8lr_VHejNUOEs2S1RCNTRFMjMwRVpJTE44WDZYUkZYVi4u", "_blank")?.focus();
    }
    const itemProps = {
        onClick: () => setMenuIndex(null)
    };

    return (
        <List
            orientation="horizontal"
            role="menubar"
            sx={{
                color: 'white',
                px: 2,
                maxWidth: 'fit-content',
                '--List-item-radius': '8px',
            }}
        >
            <ListItem>
                <Button size={"md"} color={"success"} onClick={PublishMetadata}>
                    Download<br/>Metadata
                </Button>
            </ListItem>
            <ListItem>
                <MenuButton
                    open={menuIndex === 0}
                    onOpen={() => setMenuIndex(0)}
                    onMouseEnter={() => {
                        if (typeof menuIndex === 'number') {
                            setMenuIndex(0);
                        }
                    }}
                    ref={(instance: HTMLElement) => {
                        menus.current[0] = instance;
                    }}
                    menu={
                        <Menu
                            onClose={() => {
                                menus.current[0]?.focus();
                                setMenuIndex(null);
                            }}
                        >
                            <ListItem nested>
                                <List>
                                    <MenuItem {...itemProps} onClick={NewProject}>New</MenuItem>
                                    <MenuItem {...itemProps} onClick={OpenProject}>Open</MenuItem>
                                    <ListDivider/>
                                    <MenuItem {...itemProps} onClick={SaveProject}>Save</MenuItem>
                                    <MenuItem {...itemProps} onClick={CompareProject}>Compare to Version...</MenuItem>
                                    <ListDivider/>
                                    <MenuItem {...itemProps} onClick={CloseProject}>Close</MenuItem>
                                </List>
                            </ListItem>
                        </Menu>
                    }
                >
                    File
                </MenuButton>
            </ListItem>
            <ListItem>
                <MenuButton
                    open={menuIndex === 1}
                    onOpen={() => setMenuIndex(1)}
                    onMouseEnter={() => {
                        if (typeof menuIndex === 'number') {
                            setMenuIndex(1);
                        }
                    }} ref={(instance: any) => {
                    menus.current[1] = instance;
                }}
                    menu={
                        <Menu
                            onClose={() => {
                                menus.current[1]?.focus();
                                setMenuIndex(null);
                            }}
                        >
                            <ListItem nested>
                                <List>
                                    <MenuItem {...itemProps} onClick={ShowAbout}>About</MenuItem>
                                    <MenuItem {...itemProps} onClick={ReportIssue}>Report Issue / Feedback</MenuItem>
                                </List>
                            </ListItem>
                        </Menu>
                    }
                >
                    Help
                </MenuButton>
            </ListItem>
            <ListItem>
                <Stack direction={"column"} spacing={0} display={"flex"}>
                    <Typography textColor={"#EAEAEA"} justifyContent={"right"} level={"h4"}>
                        {project?.name}
                    </Typography>
                    <Typography display={"flex"} textColor={"#EAEAEA"} justifyContent={"right"} level={"body3"}>
                        Version: {project?.version}
                    </Typography>
                </Stack>
            </ListItem>
        </List>
    );
}

const MenuButton: ForwardRefExoticComponent<any> = React.forwardRef(
    ({children, menu, open, onOpen, onKeyDown, ...props}, ref) => {
        const buttonRef = React.useRef<any>();
        const menuActions = React.useRef<any>();
        const combinedRef = React.useMemo<any>(() => {
            return (instance: any) => {
                if (instance != null && ref != null) {
                    //ref(instance);
                    buttonRef.current = instance;
                }
            };
        }, [buttonRef, ref]);


        return (
            <React.Fragment>
                <ListItemButton
                    {...props}
                    ref={combinedRef}
                    role="menuitem"
                    aria-expanded={open ? 'true' : undefined}
                    aria-controls={open ? `toolbar-example-menu-${children}` : undefined}
                    onClick={onOpen}
                    sx={{
                        color: "#EAEAEA",
                    }}
                >
                    {children}
                </ListItemButton>
                {React.cloneElement(menu, {
                    open,
                    actions: menuActions,
                    anchorEl: buttonRef.current,
                    slotProps: {
                        listbox: {
                            id: `toolbar-example-menu-${children}`,
                            'aria-label': children,
                        },
                    },
                    placement: 'bottom-start',
                    disablePortal: false,
                    variant: 'soft',
                    sx: (theme: { variants: { solid: { primary: any; }; }; }) => ({
                        width: 288,
                        boxShadow: '0 2px 8px 0px rgba(0 0 0 / 0.38)',
                        '--List-padding': 'var(--List-divider-gap)',
                        '--List-item-minHeight': '32px',
                        [`& .${menuItemClasses.root}`]: {
                            transition: 'none',
                            '&:hover': {
                                ...theme.variants.solid.primary,
                                [`& .${typographyClasses.root}`]: {
                                    textColor: '#000000',
                                },
                            },

                        },
                    }),
                })}
            </React.Fragment>
        );
    },
);
