import React, {FC, useEffect} from "react";
import {useRecoilState} from "recoil";
import {SourceApiClient} from "../../api-client/SourceApiClient";
import {AvailableClassificationsAtom} from "../../atoms/AvailableClassificationsAtom";
import {AvailableLoadTypesAtom} from "../../atoms/AvailableLoadTypesAtom";
import {CurrentProjectAtom} from "../../atoms/project-atoms/CurrentProjectAtom";
import {CompareProjectDialog} from "../Dialogs/CompareProjectVersionsDialog";
import {ImportDatasetsModal} from "../Dialogs/ImportDatasetsModal";
import {NewProjectModal} from "../Dialogs/NewProjectModal";
import {OpenProjectDialog} from "../Dialogs/OpenProjectDialog";
import {SaveProjectDialog} from "../Dialogs/SaveProjectDialog";
import {AlertSnackbar} from "../Notification/AlertSnackbar";
import {ProjectWorkspace} from "./ProjectWorkspace";
import {WelcomeAndSetup} from "./WelcomeAndSetup";

export const App: FC = () => {
    const [openProject,] = useRecoilState(CurrentProjectAtom);
    const [, setClassifications] = useRecoilState(AvailableClassificationsAtom)
    const [, setLoadTypes] = useRecoilState(AvailableLoadTypesAtom)

    useEffect(() => {
        const updateAppConfigValues = async () => {
            const sourceClient = new SourceApiClient();
            const classifications = await sourceClient.ListClassifications();
            const loadTypes = await sourceClient.ListLoadTypes();

            setClassifications(classifications.data);
            setLoadTypes(loadTypes.data);
        }

        updateAppConfigValues().then();
    }, []);

    return <React.Fragment>
        <AlertSnackbar/>
        <NewProjectModal/>
        <OpenProjectDialog/>
        <SaveProjectDialog/>
        <CompareProjectDialog/>
        <ImportDatasetsModal/>

        {!openProject || !openProject.name ? <WelcomeAndSetup/> : <ProjectWorkspace/>}
    </React.Fragment>;
};