import React, {FC} from "react";
import {serviceType} from "../../../../api/enums/serviceType";
import serviceConnectionDetail from "../../../../api/serviceConnectionDetail";
import {ServiceConnectionContainerNameField} from "../FormFields/ServiceConnectionContainerNameField";
import {ServiceConnectionPasswordField} from "../FormFields/ServiceConnectionPasswordField";
import {ServiceConnectionServerAddressField} from "../FormFields/ServiceConnectionServerAddressField";
import {ServiceConnectionUsernameField} from "../FormFields/ServiceConnectionUsernameField";
import {ServiceConnectionFormLayout} from "../ServiceConnectionFormLayout";

export interface ProjectServiceConnectionProps {
    existingSource: serviceConnectionDetail;
    updateSource: (source: serviceConnectionDetail) => void;
    selectedServiceType: serviceType;
    setIsConnected: (isConnected: boolean) => void;
}

export const PersistedStorageConnectionForm: FC<ProjectServiceConnectionProps> = (props: ProjectServiceConnectionProps) => {
    return <ServiceConnectionFormLayout isEditing={false} isProject={true} setIsConnected={props.setIsConnected}
                                        connection={props.existingSource}>
        <ServiceConnectionServerAddressField updateConnectionDetails={props.updateSource}
                                             existing={props.existingSource}/>
        <ServiceConnectionContainerNameField updateConnectionDetails={props.updateSource}
                                             existing={props.existingSource}/>
        <ServiceConnectionUsernameField updateConnectionDetails={props.updateSource}
                                        existing={props.existingSource}/>
        <ServiceConnectionPasswordField updateConnectionDetails={props.updateSource} existing={props.existingSource}/>
    </ServiceConnectionFormLayout>;
};