import React, {FC, useEffect, useState} from "react";
import {useRecoilState} from "recoil";
import {CurrentProjectAtom} from "../../atoms/project-atoms/CurrentProjectAtom";
import {AlertSnackbarAtom, AlertSnackbarContentAtom} from "../Notification/AlertSnackbar";
import {Alert, Box, CircularProgress, FormLabel, Input, Modal, ModalDialog, Typography} from "@mui/joy";
import FormControl from "@mui/joy/FormControl";
import {StyledButton} from "../utility/StyledComponentStyles";
import {IsSaveProjectDialogOpenAtom} from "../../atoms/ModalAtoms";
import {createRegExp, digit, exactly, oneOrMore} from "magic-regexp";
import {ProjectApiClient} from "../../api-client/ProjectApiClient";

export const SaveProjectDialog: FC = () => {
    const [open, setOpen] = useRecoilState(IsSaveProjectDialogOpenAtom);

    const [openProject, setOpenProject] = useRecoilState(CurrentProjectAtom);

    const [newVersion, setNewVersion] = useState(openProject?.version?.toString() ?? "0.0");

    const [, setOpenSnack] = useRecoilState(AlertSnackbarAtom);
    const [, setSnackContent] = useRecoilState(AlertSnackbarContentAtom);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState("");

    useEffect(() => {
        setNewVersion(openProject?.version?.toString() ?? "0.0");
    }, [openProject]);
    const parseNewVersion = (event: { target: any }) => {
        const NUMERIC_REGEXP = /^-?\d*(?:\.|\.\d{1,2})?$/;
        const versionExpression = createRegExp(oneOrMore(digit).and(exactly(".").and(oneOrMore(digit))));


        const val: string = event.target.value;
        if (val === "") {
            setNewVersion(val);
            return;
        }

        const newVal = val.match(versionExpression);
        if (!newVal) return;
        setNewVersion(newVal[0]!);
    };

    const SaveProject = async () => {
        setLoading(true);
        const client = new ProjectApiClient();
        const updatedProject = {...openProject!, version: Number.parseFloat(newVersion)};
        client.SaveProject(updatedProject).then((result) => {
            setLoading(false);
            if (result.success) {
                setSnackContent(<Alert color={"success"}>Saved!</Alert>);
                setOpenSnack(true);
                setOpen(false);
                setOpenProject(updatedProject);
            } else {
                setError(result.errors![0]);
                setSnackContent(<Alert color={"danger"}>Something went wrong...</Alert>);
                setOpenSnack(true);
            }
        });
    };

    return (<Modal open={open} onClose={() => setOpen(false)}>
        <ModalDialog size={"lg"}>
            <Box>
                <Typography
                    level={"h5"}
                    marginBottom={2}>
                    Save Project</Typography>
                <FormControl>
                    <FormLabel>Project</FormLabel>
                    <Input value={openProject?.name} disabled size={"md"} fullWidth sx={{marginBottom: 2}}/>
                </FormControl>
                <FormControl>
                    <FormLabel>Version</FormLabel>
                    <Input type={"number"} value={newVersion} size={"md"} fullWidth sx={{marginBottom: 2}}
                           onChange={event => {
                               setNewVersion(event.target.value);
                           }}/>
                </FormControl>
                <Box display={"flex"} justifyContent={"space-between"}>
                    <StyledButton sx={{minWidth: "160px"}} variant="outlined"
                                  onClick={() => setOpen(false)}>Cancel</StyledButton>
                    <StyledButton disabled={loading} sx={{minWidth: "160px"}} onClick={SaveProject}>{loading ?
                        <CircularProgress/> : "Save"}</StyledButton>
                </Box>
                {error && <Typography>{error}</Typography>}
            </Box>
        </ModalDialog>
    </Modal>);
};