import apiRequest from "../api/apiRequest";
import Project from "../api/project";
import serviceConnectionDetail from "../api/serviceConnectionDetail";
import {BaseApiClient} from "./BaseApiClient";
import apiResponse from "../api/apiResponse";

export class ProjectApiClient extends BaseApiClient {
    constructor() {
        super("project");
    }

    async ListProjects(data: serviceConnectionDetail): Promise<apiResponse<Project[]> | undefined> {
        const payload: apiRequest<serviceConnectionDetail> = {data: data};
        const result = await this._post<Project[], serviceConnectionDetail>("list", payload);
        return result;
    }

    async SaveProject(data: Project): Promise<apiResponse<boolean>> {
        const payload: apiRequest<Project> = {data: data};
        return await this._post<boolean, Project>("save", payload);
    }

    async ExportProject(data: Project) {
        const payload: apiRequest<Project> = {data: data};

        return await this._postAndDownload("export", payload);
    }
}