import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import {Alert, Button, CircularProgress, Grid, ListDivider, Typography} from "@mui/joy";
import {AccordionDetails, AccordionSummary} from "@mui/material";
import React, {FC, useState} from "react";
import {useRecoilState} from "recoil";
import {SourceApiClient} from "../../../api-client/SourceApiClient";
import {AlertSnackbarAtom, AlertSnackbarContentAtom} from "../../Notification/AlertSnackbar";
import {StyledAccordion, StyledButton} from "../../utility/StyledComponentStyles";
import {ServiceConnectionProps} from "../ServiceConnectionProps";

export const ServiceConnectionFormLayout: FC<ServiceConnectionProps> = (props) => {
    const [isTestingConnection, setIsTestingConnection] = useState(false);
    const [errorMsg, setErrorMsg] = useState<string | null>();
    const [, setSnackBarOpen] = useRecoilState(AlertSnackbarAtom);
    const [, setSnackContent] = useRecoilState(AlertSnackbarContentAtom);

    const TestConnection = async () => {
        if (props.connection === undefined || props.connection === null) {
            return;
        }
        setIsTestingConnection(true);
        const client = new SourceApiClient();
        const result = await client.TestConnection(props.connection);
        setIsTestingConnection(false);
        setSnackContent(<Alert
            color={result.success ? "success" : "danger"}> {result?.data ? "Connected!" : "Something went wrong..."} </Alert>);
        setSnackBarOpen(true);

        props.setIsConnected?.(result.data);
        setErrorMsg(result?.errors[0] ?? "");
    };

    return (<Grid container spacing={3} rowSpacing={2} display={"flex"}>
        {props.children}
        <Grid xs={12}>
            <StyledButton fullWidth size={"md"} variant="outlined" onClick={async () => await TestConnection()}
                          disabled={isTestingConnection}>{isTestingConnection ?
                <CircularProgress size="sm"/> : "Test Connection"}</StyledButton>
        </Grid>
        {errorMsg && <Grid xs={12}>
            <label>{errorMsg}</label>
        </Grid>}
        <Grid xs={12}>
            <ListDivider/>
        </Grid>
        {!props.isProject && <Grid xs={12} display={"flex"} justifyContent={"space-between"} gap={1}>
            <StyledButton fullWidth
                          variant="outlined"
                          size={"md"}
                          onClick={props.cancel}
                          disabled={isTestingConnection}>Cancel</StyledButton>

            <StyledButton fullWidth size={"md"} variant={"solid"} onClick={props.save!}
                          disabled={isTestingConnection}>Save</StyledButton>
        </Grid>}
        {props.isEditing && !props.isProject && <Grid xs={12}>
            <StyledAccordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon/>}
                    aria-controls="panel3a-content"
                    id="panel3a-header"
                    sx={{pb: 0}}
                >
                    <Typography fontWeight={600}>Advanced</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Button variant="soft" color={"danger"} fullWidth size={"md"} onClick={props.delete}
                            disabled={isTestingConnection}>Delete</Button>
                </AccordionDetails>
            </StyledAccordion>
        </Grid>}
    </Grid>)
};