import {ArrowDropDown, ArrowRight} from "@mui/icons-material";
import {FormControl, Grid, Input} from "@mui/joy";
import {TreeView} from "@mui/lab";
import React, {FC, useEffect, useMemo, useState} from "react";
import sourceDataset from "../../../api/sourceDataset";
import {DatasetTransferListProps, sortDataSet} from "../DatasetTransferList";
import {FileBrowserPopup} from "./FileBrowserPopup";
import {FileBrowserTreeNode} from "./FileBrowserTreeNode";


export const FileBrowserList: FC<DatasetTransferListProps> = (props) => {
    const [searchText, setSearchText] = useState<string>("");

    const [availableDatasets, setAvailableDatasets] = useState<sourceDataset[]>(props.left.map(x => props.right.find(y => y.name === x.name) ?? x)); //props.left.filter(x => !props.right.map(y => y.name).includes(x.name)));
    const [checkedDatasetNames, setCheckedDatasetNames] = useState<string[]>(props.right.map(x => x.name));
    const displayAvailableDatasets = useMemo(() => availableDatasets.filter(x => x.name.toLowerCase().includes(searchText.toLowerCase())).sort(sortDataSet), [availableDatasets, searchText]);
    const displayedStructuredAvailableDatasets = useMemo(() => {
        const paths = displayAvailableDatasets.map(x => {
            return x.name;
        })
        const result: any = {};
        paths.forEach(p => p.split('/').reduce((o, k) => o[k] = o[k] || {}, result));
        return result;
    }, [displayAvailableDatasets]);
    const searchTextChanged = (event: React.ChangeEvent<HTMLInputElement>) =>
        setSearchText(event.target.value);

    const UpdateParserOption = (ds: sourceDataset | undefined, option: string, value: string) => {
        if (!ds) return;
        let newDs: sourceDataset;

        if (option.includes('.')) {
            const parts = option.split('.');
            switch (parts[0]) {
                case 'csv':
                    newDs = {
                        ...ds,
                        parserOptions: {...ds.parserOptions, csv: {...ds.parserOptions.csv as any, [parts[1]]: value}}
                    };
                    break;
                case 'fixedWidth':
                    newDs = {
                        ...ds,
                        parserOptions: {
                            ...ds.parserOptions,
                            fixedWidth: {...ds.parserOptions.fixedWidth as any, [parts[1]]: value}
                        }
                    };
                    break;
            }
        } else {
            newDs = {...ds, parserOptions: {...ds.parserOptions, [option]: value}};
        }
        const newAvailableDatasets = availableDatasets.map(x => x.name === newDs.name ? newDs : x);
        setAvailableDatasets(newAvailableDatasets);
    }

    const [popoverAnchorEl, setPopoverAnchorEl] = React.useState<HTMLElement | null>(null);
    const [popoverDataset, setPopoverDataset] = useState<sourceDataset | null>(null);
    const handlePopoverOpen = (event: React.MouseEvent<HTMLElement>, ds: sourceDataset) => {
        setPopoverDataset(ds);
        setPopoverAnchorEl(event.currentTarget);
    };

    const handleUpdateDataset = (ds: sourceDataset) => {
        const newAvailableDatasets = availableDatasets.map(x => x.name === ds.name ? ds : x);
        setAvailableDatasets(newAvailableDatasets);
    }
    useEffect(() => {
        props.setDatasetsToImport(availableDatasets.filter(x => checkedDatasetNames.includes(x.name)));
    }, [checkedDatasetNames]);


    return (
        <React.Fragment>
            <FileBrowserPopup popoverAnchorEl={popoverAnchorEl}
                              source={props.source}
                              dataset={popoverDataset}
                              setPopoverAnchorEL={setPopoverAnchorEl}
                              updateDataset={handleUpdateDataset}
            />
            <Grid container spacing={2} justifyContent="center" alignItems="stretch">
                <Grid xs={12}>
                    <FormControl>
                        <Input type={"text"} placeholder="Search Datasets" fullWidth onChange={searchTextChanged}
                               value={searchText}/>
                    </FormControl>
                </Grid>
                <Grid xs={12} display={"flex"} flexGrow={1}>
                    <TreeView defaultCollapseIcon={<ArrowDropDown/>}
                              defaultExpandIcon={<ArrowRight/>}
                              defaultEndIcon={<div style={{width: 24}}/>}
                              sx={{height: 264, flexGrow: 1, maxWidth: 900, overflowY: 'auto'}}
                    >
                        {Object.keys(displayedStructuredAvailableDatasets).map((key) => {
                            return FileBrowserTreeNode(availableDatasets,
                                displayedStructuredAvailableDatasets[key],
                                key,
                                "",
                                0,
                                checkedDatasetNames,
                                setCheckedDatasetNames,
                                handlePopoverOpen);
                        })}
                    </TreeView>
                </Grid>
            </Grid>
        </React.Fragment>
    )

}