import {Box, FormLabel, Input, Modal, ModalDialog, Typography} from "@mui/joy";
import FormControl from "@mui/joy/FormControl";
import React, {FC, useState} from "react";
import {useRecoilState} from "recoil";
import {IsNewProjectDialogOpenAtom} from "../../atoms/ModalAtoms";
import {CurrentProjectAtom} from "../../atoms/project-atoms/CurrentProjectAtom";
import {StyledButton} from "../utility/StyledComponentStyles";

export const NewProjectModal: FC = () => {
    const [isOpen, setIsOpen] = useRecoilState(IsNewProjectDialogOpenAtom);
    const [project, setProject] = useRecoilState(CurrentProjectAtom);

    const [newProjectName, setNewProjectName] = useState("");

    const CreateProject = async () => {
        setProject({...project!, name: newProjectName, version: 0.0});
        setIsOpen(false);
    };

    return (<Modal open={isOpen} onClose={() => setIsOpen(false)}>
        <ModalDialog size={"lg"}>
            <Box>
                <Box>
                    <Typography
                        level={"h3"}
                        marginBottom={2}>
                        New Project</Typography>
                </Box>

                <FormControl required>
                    <FormLabel>Project Name</FormLabel>
                    <Input size={"md"} fullWidth
                           sx={{marginBottom: 2}}
                           value={newProjectName}
                           onChange={(e: { target: { value: React.SetStateAction<string>; }; }) => setNewProjectName(e.target.value)}/>
                </FormControl>

                <Box display={"flex"} justifyContent={"space-between"}>
                    <StyledButton sx={{minWidth: "160px"}} variant="outlined"
                                  onClick={() => setIsOpen(false)}>Cancel</StyledButton>
                    <StyledButton sx={{minWidth: "160px"}} disabled={!newProjectName}
                                  onClick={CreateProject}>Create</StyledButton>
                </Box>
            </Box>
        </ModalDialog>
    </Modal>);
};