import { Check } from "@mui/icons-material";
import
    {
        Chip,
        Divider,
        List,
        ListDivider,
        ListItem,
        ListItemDecorator,
        listItemDecoratorClasses,
        Option,
        optionClasses,
        Select,
        Stack,
        Typography
    } from "@mui/joy";
import React, { FC } from "react";
import { serviceType } from "../../../api/enums/serviceType";
import { toTitleCase } from "../../../Utility";


export interface ServiceTypeSelectionFormProps
{
    selectedService: serviceType | null;
    isProject: boolean;

    setService(service: serviceType): void;
}

export const ServiceGroupings = {
    "File": [serviceType.FILE_AZURE_BLOB, serviceType.FILE_S3, serviceType.FILE_SMB],
    "Database": [serviceType.DATABASE_AZURE_SQL, serviceType.DATABASE_AZURE_SYNAPSE_SQL, serviceType.DATABASE_MICROSOFT_SQL_SERVER, serviceType.DATABASE_ORACLE_SQL],
};
export const LabelForService = (o: serviceType) =>
{
    return toTitleCase(o.substring(o.indexOf("_") + 1).replaceAll("_", " "));
};
export const GetServiceGroupNameForServiceType = (serviceType: serviceType) =>
{
    return Object.entries(ServiceGroupings).find(([name, opts]) => opts.includes(serviceType))?.[0] ?? "";
};
export const ServiceGroupChip = (name: string) =>
{
    return <Chip variant={"soft"}
        size="sm"
        sx={{ borderRadius: "xs" }}
    >
        {name}
    </Chip>;
};
const AvailableProjectTypes = [serviceType.FILE_AZURE_BLOB, serviceType.FILE_S3];

export const ServiceTypeSelectionForm: FC<ServiceTypeSelectionFormProps> = (props) =>
{

    return <Stack direction={"column"} flex={"Grow"} display={"flex"} width={"100%"} alignItems={"stretch"}>
        {props.isProject && <React.Fragment>
            <Typography fontSize={"lg"} display={"flex"} justifyContent={"center"}>Select a service to store your
                project files</Typography>
            <Divider />
            <br />
        </React.Fragment>}

        <Select sx={{ width: '100%' }} placeholder={"Connect to..."} value={props.selectedService}
            onChange={(event, newVal) => props.setService(newVal as serviceType)}>
            {Object.entries(ServiceGroupings).map(([name, opts], index) => (
                <React.Fragment key={name}>
                    {index !== 0 && <ListDivider role="none" />}
                    <List>
                        <ListItem id={`select-group-${name}`} sticky>
                            <Typography level="h6">
                                {name}
                            </Typography>
                        </ListItem>
                        {opts.map((o) => (<Option
                            key={o}
                            disabled={props.isProject && !AvailableProjectTypes.includes(o)}
                            value={o}
                            label={<React.Fragment>
                                {ServiceGroupChip(name)}
                                {" "}
                                {LabelForService(o)}
                            </React.Fragment>}
                            sx={{
                                [`&.${optionClasses.selected} .${listItemDecoratorClasses.root}`]: {
                                    opacity: 1,
                                },
                            }}
                        >
                            <ListItemDecorator sx={{ opacity: 0 }}>
                                <Check />
                            </ListItemDecorator>
                            {LabelForService(o)}
                        </Option>))}
                    </List>
                </React.Fragment>))}
        </Select>
    </Stack>;
};