import axios from "axios";
import apiRequest from "../api/apiRequest";
import apiResponse from "../api/apiResponse";

export abstract class BaseApiClient {
    protected readonly _baseUrl: string;
    protected _headers: Headers;

    protected constructor(baseEndpoint: string) {
        this._baseUrl = "/api/" + baseEndpoint + "/";
        this._headers = new Headers();
        this._headers.append("Content-Type", "application/json");
        this._headers.append("Accept", "application/json");
        this._headers.append("Content-Encoding", "gzip");
    }

    protected async _get<T>(url: string) {
        const res = await axios.get<apiResponse<T>>(this._baseUrl + url);
        return res.data as apiResponse<T>;
    }

    protected async _post<T, P>(url: string, data: apiRequest<P>) {
        const res = await axios.post<apiResponse<T>>(this._baseUrl + url, data, {headers: {"Content-Type": "application/json"}});
        return res.data as apiResponse<T>;
    }

    protected async _postAndDownload<P>(url: string, data: P) {
        return await axios.post(this._baseUrl + url, data, {
            responseType: "blob",
            headers: {"Content-Type": "application/json"}
        });
    }
}