import { FormControl, FormLabel, Grid, Input } from "@mui/joy";
import React, { FC } from "react";
import { serviceType } from "../../../../api/enums/serviceType";
import serviceConnectionDetail from "../../../../api/serviceConnectionDetail";
import { ServiceConnectionFieldProps } from "./ServiceConnectionFieldProps";
import { LastUsedAWSRegion } from "../../../../atoms/LastUsedAWSRegionAtom";
import { useRecoilState } from "recoil";

export const ServiceConnectionServerAddressField: FC<ServiceConnectionFieldProps> = (props: ServiceConnectionFieldProps) =>
{
    const [region,] = useRecoilState(LastUsedAWSRegion);
    return (
        <Grid xs={12}>
            <FormControl required={true}>
                <FormLabel>{GetLabelForServiceType(props.existing!.serviceType)}</FormLabel>
                <Input fullWidth={true}
                    name={"serverAddress"}
                    value={props.existing?.serverAddress}
                    onChange={(x) => props.updateConnectionDetails({
                        ...props.existing, serverAddress: x.target.value
                    } as serviceConnectionDetail)}
                    defaultValue={props.existing?.serviceType === serviceType.FILE_S3 ? region ?? undefined : undefined}
                />
            </FormControl>
        </Grid>);
};

const GetLabelForServiceType = (s: serviceType): string =>
{
    switch (s)
    {
        case serviceType.FILE_S3:
            return "Region";
        case serviceType.FILE_AZURE_BLOB:
            return "Storage Account Name";
        default:
            return "Server Address";
    }
};