import React, {FC, useEffect, useMemo, useState} from "react";
import {useRecoilState} from "recoil";
import {CurrentProjectAtom} from "../../atoms/project-atoms/CurrentProjectAtom";
import Project from "../../api/project";
import {Box, CircularProgress, FormLabel, Grid, Modal, ModalDialog, Option, Select, Typography} from "@mui/joy";
import FormControl from "@mui/joy/FormControl";
import DiffViewer from "react-diff-viewer-continued";
import {IsCompareProjectDialogOpenAtom} from "../../atoms/ModalAtoms";
import {GetProjectsFromApi} from "./OpenProjectDialog";
import {AlertSnackbarAtom, AlertSnackbarContentAtom} from "../Notification/AlertSnackbar";
import {UniqueArray} from "../utility/HelperFunctions";
import projectSource from "../../api/projectSource";

export const CompareProjectDialog: FC = () => {
    const [openProject,] = useRecoilState(CurrentProjectAtom);
    const [open, setOpen] = useRecoilState(IsCompareProjectDialogOpenAtom);

    const [loading, setLoading] = useState(true);
    const [oldCodeSelectedVersion, setOldCodeSelectedVersion] = useState<Number | null>(null);
    const [remoteProjects, setRemoteProjects] = useState<Project[]>([]);
    const [oldCode, setOldCode] = useState<Project | null>(null);
    const [newCode, setNewCode] = useState<Project | null>(null);
    const [error, setError] = useState("");
    const [isSnackOpen, setIsSnackOpen] = useRecoilState(AlertSnackbarAtom);
    const [snackContent, setSnackContent] = useRecoilState(AlertSnackbarContentAtom);

    const CleanSources = (sources: projectSource[]) => {
        return sources.map(x => {
            return {...x, sourceConnection: {...x.sourceConnection, password: ""}}
        })
    };
    const displayableProject = useMemo(() => {
        return {...openProject, sources: CleanSources(openProject?.connections ?? [])}
    }, [openProject]);


    useEffect(() =>
    {
        if (!open) return;
        alert('Warning. Comparing project versions is known to hang and become unresponsive.')
        setLoading(true);
        GetProjectsFromApi(setError, setLoading, openProject!, setRemoteProjects, setIsSnackOpen, setSnackContent);
        setNewCode(openProject!);
        setLoading(false);
    }, [open]);

    return <Modal open={open} onClose={() => setOpen(false)}>
        <ModalDialog size={"lg"} sx={{maxHeight: "70%", overflowY: "scroll"}}>
            {loading ? <CircularProgress size={"lg"}/> : <React.Fragment>
                <Grid container>
                    <Grid xs={3}>
                        <FormControl>
                            <FormLabel>Version</FormLabel>
                            <Select value={oldCodeSelectedVersion}
                                    onChange={(ev, val) => {
                                        setOldCode(remoteProjects.find(x => x.name === openProject?.name && x.version === val! as Number)!);
                                        setOldCodeSelectedVersion(val! as Number);
                                    }}>
                                {UniqueArray(remoteProjects.filter((x) => x.name === openProject!.name!).map(x => x.version)).sort().map((x) =>
                                    <Option key={`${x}.old`} value={x}>{x}</Option>)}
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid xs={6}>
                        <Typography textAlign={"center"}>Compare Project<br/>{openProject?.name}</Typography>
                    </Grid>
                    <Grid xs={3}>
                        <FormControl>
                            <FormLabel>Version</FormLabel>
                            <Select disabled value={"current"}>
                                <Option value={"current"}>Current</Option>
                            </Select>
                        </FormControl>
                    </Grid>
                </Grid>
                <br/>
                <Box overflow={"auto"}>
                    <DiffViewer oldValue={!oldCode ? "" : JSON.stringify(oldCode, null, 2)}
                                newValue={!newCode ? "" : JSON.stringify(displayableProject, null, 2)}
                                splitView={true}/>
                </Box>
            </React.Fragment>}
        </ModalDialog>
    </Modal>;
};