import {atom} from "recoil";
import {serviceType} from "../../api/enums/serviceType";
import project from "../../api/project";
import {persistAtom} from "../Persisted-Atom-In-Storage";

export const CurrentProjectAtom = atom<project | null>({
    key: "current-project",
    default: {projectStore: {serviceType: serviceType.FILE_AZURE_BLOB}} as project,
    effects: [persistAtom]
});