import {Box, Button, Sheet, Stack, Typography} from "@mui/joy";
import {Theme} from "@mui/joy/styles";
import {GlobalStyles} from "@mui/system";
import React, {FC, ReactElement, useState} from "react";
import {EditorPanel} from "../Editor/EditorPanel";
import {SourcesPage} from "../Sources/SourcesPage";
import {WorkspaceMenu} from "./WorkspaceMenu";
import {DatasetsPage} from "../Datasets/DatasetsPage";
import {Collapse} from "@mui/material";
import {ArrowLeft, ArrowRight, Edit} from "@mui/icons-material";

interface WorkspacePanelProps {
    CanCollapse: boolean,
    title: string,
    show: boolean,
    setShow: (collapsed: boolean) => void
    Children: ReactElement;
    WidthMultiplier: number;
    minWidthMultiplier: number;
}

const WorkspacePanel: FC<WorkspacePanelProps> = (props) => {
    return <Sheet variant="outlined"
                  sx={{
                      borderRadius: 1,
                      border: 1,
                      bgcolor: "background.componentBg",
                      display: "flex",
                      flexDirection: "column",
                      height: "100%",
                      "&:hover": {
                          "boxShadow": "0 0 8px 2px #5e4ae3"
                      },
                      p: 1,
                      pt: 0,
                      flexGrow: props.CanCollapse ? 0 : 1
                  }}>
        <Stack direction={"row"} display={"flex"} alignItems={"start"} spacing={1} flexGrow={1}>
            <Button disabled={!props.CanCollapse} size={"sm"} variant={"plain"}
                    onClick={() => props.setShow(!props.show)}>
                <Typography level={"body1"}
                            sx={{
                                textOrientation: 'sideways',
                                writingMode: 'vertical-lr'
                            }}>{props.CanCollapse ? (props.show ? <ArrowLeft/> :
                    <ArrowRight/>) : <Edit/>} {props.title}</Typography>
            </Button>
            {props.CanCollapse ?
                <Collapse in={props.show} orientation={"horizontal"}>
                    <Box flexGrow={1} display={"flex"} flexDirection={"row"} padding={1}
                         sx={{minWidth: 'calc(100vw * ' + props.minWidthMultiplier + ')'}}>
                        {props.Children}
                    </Box>
                </Collapse> : props.Children}
        </Stack>
    </Sheet>
}
export const ProjectWorkspace: FC = () => {
    const [showSources, setShowSources] = useState<boolean>(true);
    const [showDatasets, setShowDatasets] = useState<boolean>(true);

    return <React.Fragment>
        <GlobalStyles<Theme>
            styles={(theme) => ({
                body: {
                    margin: 0, fontFamily: theme.vars.fontFamily.body, // background: 'linear-gradient(225deg, #1F018A
                                                                       // 24.88%, #12A5DD 75.12%)',
                    backgroundColor: "#042047", height: "100vh"
                },
            })}
        />
        <Stack direction={"column"} display={"flex"} sx={{maxWidth: "100vw"}}>
            <Box display={"flex"} justifyContent={"stretch"} sx={
                {
                    bgcolor: 'transparent',
                    display: 'flex',
                    flexDirection: 'row',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    flexGrow: 1
                }}
            >
                <img
                    alt="Versor Logo"
                    src="metadata-manager-logo-horizontal.png"
                    className="logo-versor"
                    style={{
                        maxWidth: "180px"
                    }}
                />
                <WorkspaceMenu/>
            </Box>
            <Stack direction={"row"} display={"flex"} flexGrow={1} spacing={1}
                   sx={{
                       height: "calc(100vh - 90px)",
                       p: 1,
                   }}>
                <WorkspacePanel CanCollapse={true} title={"Sources"} show={showSources} setShow={setShowSources}
                                Children={<SourcesPage/>} WidthMultiplier={0.2} minWidthMultiplier={0.2}/>
                <WorkspacePanel CanCollapse={true} title={"Datasets"} show={showDatasets} setShow={setShowDatasets}
                                Children={<DatasetsPage/>} WidthMultiplier={0.1} minWidthMultiplier={0.1}/>
                <WorkspacePanel CanCollapse={false} title={"Editor"} show={true} setShow={() => {
                }}
                                Children={<EditorPanel/>} WidthMultiplier={0.7} minWidthMultiplier={0.7}/>
            </Stack>
        </Stack>
    </React.Fragment>
}