import {serviceType} from "../api/enums/serviceType";

export const IsServiceTypeDatabase = (ServiceType: serviceType) => {
    const databaseEnums = [serviceType.DATABASE_AZURE_SQL, serviceType.DATABASE_ORACLE_SQL, serviceType.DATABASE_AZURE_SYNAPSE_SQL, serviceType.DATABASE_MICROSOFT_SQL_SERVER];
    return databaseEnums.includes(ServiceType);
}