import React, {FC, ReactElement, useEffect, useMemo, useState} from "react";
import {useRecoilState} from "recoil";
import {CurrentProjectAtom} from "../../atoms/project-atoms/CurrentProjectAtom";
import {AlertSnackbarAtom, AlertSnackbarContentAtom} from "../Notification/AlertSnackbar";
import {
    Alert,
    Box,
    CircularProgress,
    Divider,
    List,
    ListItem,
    ListItemButton,
    ListItemContent,
    Modal,
    ModalDialog,
    Option,
    Select,
    Stack,
    Typography
} from "@mui/joy";
import {StyledButton} from "../utility/StyledComponentStyles"
import {IsOpenProjectDialogOpenAtom} from "../../atoms/ModalAtoms";
import Project from "../../api/project";
import {ProjectApiClient} from "../../api-client/ProjectApiClient";
import {UniqueArray} from "../utility/HelperFunctions";

export const GetProjectsFromApi = (setError: (e: string) => void, setLoading: (l: boolean) => void, openProject: Project, setRemoteProjects: (p: Project[]) => void, setOpenSnack: (op: boolean) => void, setSnackContent: (c: ReactElement) => void) => {
    setError("");
    setLoading(true);
    const client = new ProjectApiClient();
    client.ListProjects(openProject!.projectStore).then((result) => {
        if (result?.success) {
            setRemoteProjects(result?.data);
        } else {
            setRemoteProjects([]);
            setOpenSnack(true);
            setSnackContent(<Alert color={"danger"}>Something went wrong...</Alert>);
            setError(result?.errors[0] ?? "");
        }
        setLoading(false);
    });
}

export const OpenProjectDialog: FC = () => {
    const [open, setOpen] = useRecoilState(IsOpenProjectDialogOpenAtom);
    const [project, setProject] = useRecoilState(CurrentProjectAtom);

    const [remoteProjects, setRemoteProjects] = useState<Project[]>([]);
    const [searchText, setSearchText] = useState("");

    const projectsToDisplay = useMemo(() => {
        return (searchText === ""
                ? remoteProjects
                : remoteProjects
                    .filter((x) => x.name.toLowerCase().includes(searchText.toLowerCase()))
        ).sort((a, b) => {
            if (a.name === b.name) {
                if (a.version < b.version) return -1;
                if (a.version > b.version) return 1;
                return 0;
            } else {
                if (a.name < b.name) return -1;
                if (a.name > b.name) return 1;
                return 0;
            }
        });
    }, [remoteProjects, searchText]);

    const [selectedProject, setSelectedProject] = useState<string | null>(null);
    const [selectedVersion, setSelectedVersion] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState("");
    const [, setOpenSnack] = useRecoilState(AlertSnackbarAtom);
    const [, setSnackContent] = useRecoilState(AlertSnackbarContentAtom);

    useEffect(() => {
        if (!open) return;
        GetProjectsFromApi(setError, setLoading, project!, setRemoteProjects, setOpenSnack, setSnackContent);
    }, [open]);

    const OpenProject = () => {
        const projectToOpen: Project = {
            ...remoteProjects.find(x => x.name === selectedProject && x.version === Number.parseFloat(selectedVersion!)),
            projectStore: project!.projectStore
        } as Project;
        setProject(projectToOpen);
        setOpen(false);
    }

    return (<Modal open={open} onClose={() => setOpen(false)}>
        <ModalDialog size={"lg"}>
            <Stack marginBottom={2}>
                <Typography level={"h4"}>Open Project</Typography>
                <Divider/>
                <List sx={{
                    maxHeight: '600px',
                    height: '400px',
                    overflowY: 'auto',
                }}>
                    {UniqueArray(projectsToDisplay.map(x => x.name)).map((x) => {
                        return (
                            <ListItem key={x + '.project.li'} value={x}>
                                <ListItemContent>
                                    <Stack direction={"row"} justifyContent={"space-evenly"} spacing={2}>
                                        <ListItemButton onClick={() => setSelectedProject(x)}>{x}</ListItemButton>
                                        {x === selectedProject && <Select placeholder="Version" 
                                            slotProps={{
                                            listbox: {
                                                sx: {
                                                    maxHeight: '150px',
                                                    overflowY: 'auto',
                                                    zIndex: 10000
                                                },
                                            },
                                        }}
                                            onChange={(e, newValue) => setSelectedVersion(newValue as string)}>
                                            {projectsToDisplay.filter(y => y.name === selectedProject).map((y) => y.version).map(y =>
                                            {
                                                return (
                                                    <Option key={`${x}.${y}`} value={y}>v{y}</Option>
                                                )
                                            })}
                                        </Select>}
                                    </Stack>
                                </ListItemContent>

                            </ListItem>
                        )
                    })}
                </List>
            </Stack>

            <Box display={"flex"} justifyContent={"space-between"}>
                <StyledButton sx={{minWidth: "160px"}} variant="outlined"
                              onClick={() => setOpen(false)}>Cancel</StyledButton>
                <StyledButton
                    sx={{minWidth: "160px"}}
                    disabled={selectedVersion === null || loading}
                    onClick={OpenProject}>{loading ? <CircularProgress/> : "Open"}</StyledButton>
            </Box>
            {!error ? <Typography>{error}</Typography> : null}
        </ModalDialog>
    </Modal>);
};