import {Box, Divider, Typography} from "@mui/joy";
import React, {FC, useEffect, useMemo, useRef, useState} from "react";
import AutoSizer from "react-virtualized-auto-sizer";
import {VariableSizeList} from "react-window";
import {useRecoilState} from "recoil";
import Project from "../../api/project";
import projectSource from "../../api/projectSource";
import sourceDataset from "../../api/sourceDataset";
import sourceDataSetField from "../../api/sourceDataSetField";
import {AvailableClassificationsAtom} from "../../atoms/AvailableClassificationsAtom";
import {EditorEntityChangesSavedAtom} from "../../atoms/EditorEntityChangesSavedAtom";
import {EntityToOpenInEditorAtom} from "../../atoms/EntityToOpenInEditorAtom";
import {CurrentProjectAtom} from "../../atoms/project-atoms/CurrentProjectAtom";
import {EditorHeader} from "./EditorHeader";
import {RenderRow} from "./EditorSchemaFieldRow";


export const EditorPanel: FC = () => {
    const [classifications,] = useRecoilState(AvailableClassificationsAtom);
    const [project, setProject] = useRecoilState(CurrentProjectAtom);
    const [entityToOpenInEditor, setEntityToOpenInEditor] = useRecoilState(EntityToOpenInEditorAtom);
    const [fieldSearchFilter, setFieldSearchFilter] = useState("");

    const [freshlyOpened, setFreshlyOpened] = useState(true);

    const [tempDataset, setTempDataset] = useState<sourceDataset | null>(null);
    const filteredSchemas = useMemo(() => {
        return fieldSearchFilter === "" ? tempDataset?.schema : tempDataset?.schema.filter((x) => x.name.toLowerCase().indexOf(fieldSearchFilter.toLowerCase()) >= 0)
    }, [tempDataset, fieldSearchFilter]);

    const [changesSaved, setChangesSaved] = useRecoilState(EditorEntityChangesSavedAtom);
    const [showAdvanced, setShowAdvanced] = useState<boolean[]>([]);

    const UpdateShowAdvanced = (show: boolean, index: number) => {
        let newShowAdvanced = [...showAdvanced];
        newShowAdvanced[index] = show;
        setShowAdvanced(newShowAdvanced);
    }

    useEffect(() => {
        if (entityToOpenInEditor != null && project?.connections.find(x => x.sourceName === entityToOpenInEditor?.sourceName)?.datasets.find(x => x.name === entityToOpenInEditor?.datasetName) == null) {
            setEntityToOpenInEditor(null);
        }
    }, [project?.connections]);

    useEffect(() => {
        setFieldSearchFilter("");
        setChangesSaved(true);

        if (entityToOpenInEditor != null) {
            const newTempDataset: sourceDataset = project?.connections.find(x => x.sourceName === entityToOpenInEditor?.sourceName)?.datasets.find(x => x.name === entityToOpenInEditor?.datasetName) as sourceDataset;
            setTempDataset(newTempDataset);
            setShowAdvanced(new Array(newTempDataset.schema.length).fill(false));
        }
    }, [entityToOpenInEditor]);


    const Save = () => {
        let updatedSrc = project?.connections.find(x => x.sourceName === entityToOpenInEditor?.sourceName) as projectSource;
        const updatedDatasets = updatedSrc.datasets.map((x) => x.name === tempDataset?.name ? tempDataset as sourceDataset : x);
        updatedSrc = {...updatedSrc, datasets: updatedDatasets};
        const updatedConnections = project?.connections.map((x) => x.sourceName === updatedSrc.sourceName ? updatedSrc : x);
        setProject({...project as Project, connections: updatedConnections as projectSource[]})
        setChangesSaved(true);
    };
    const RevertChanges = () => {
        setTempDataset(project?.connections.find(x => x.sourceName === entityToOpenInEditor?.sourceName)?.datasets.find(x => x.name === entityToOpenInEditor?.datasetName) as sourceDataset);
        setChangesSaved(true);
    }
    useEffect(() => {
        if (tempDataset?.name != entityToOpenInEditor?.datasetName) {
            setFreshlyOpened(true);
        } else if (tempDataset?.name === entityToOpenInEditor?.datasetName && !freshlyOpened) {
            setChangesSaved(false);
        } else {
            setFreshlyOpened(false);
        }
    }, [tempDataset, entityToOpenInEditor]);

    const UpdateVal = (field: sourceDataSetField, val: any, attributeName: string) => {
        const getNewField = () => {
            if (!attributeName.includes(".")) {
                return {...field, [attributeName]: val};
            }
            const attSplit = attributeName.split('.');
            if (attSplit[0] === "destination") {
                return {...field, destination: {...field.destination, [attributeName.split('.')[1]]: val}};
            } else if (attSplit[0] === "source") {
                return {...field, source: {...field.source, [attributeName.split('.')[1]]: val}};
            }
        }
        const newField = getNewField();
        const updatedFields = tempDataset?.schema.map((x) => x.name === field.name ? newField : x);
        setTempDataset({...tempDataset, schema: updatedFields!} as sourceDataset);
    }
    const listRef = useRef<any>({});
    const rowHeights = useRef<any>({});

    function getRowHeight(index: number) {
        return rowHeights.current[index] + 8 || 82;
    }


    return <Box sx={{
        padding: 1, display: "flex", height: "calc(100vh - 100px)", flexGrow: 1, flexDirection: "column",
    }}>
        {!entityToOpenInEditor
            ? <Typography>Select an entity reference to edit it here...</Typography>
            : <React.Fragment>
                <EditorHeader changesSaved={changesSaved!} Save={Save} fieldSearchFilter={fieldSearchFilter}
                              setFieldSearchFilter={setFieldSearchFilter} RevertChanges={RevertChanges}
                              tempDataset={tempDataset!} setTempDataset={setTempDataset}
                              entityToOpenInEditor={entityToOpenInEditor}/>
                <br/>
                <Divider/>
                <Box display={"flex"} width={'100%'} height={'100%'}>
                    <AutoSizer>
                        {({height, width}) =>
                            <VariableSizeList
                                height={height!}
                                width={width!}
                                itemCount={filteredSchemas?.length ?? 0}
                                itemSize={getRowHeight}
                                ref={listRef}
                                overscanCount={5}>
                                {({index, style}) =>
                                    <RenderRow
                                        filteredSchemas={filteredSchemas!}
                                        tempDataset={tempDataset!}
                                        showAdvanced={showAdvanced}
                                        UpdateShowAdvanced={UpdateShowAdvanced}
                                        UpdateVal={UpdateVal}
                                        rowHeights={rowHeights}
                                        listRef={listRef}
                                        setTempDataset={setTempDataset}
                                        index={index}
                                        style={style}
                                        availableClassifications={classifications}
                                    />
                                }
                            </VariableSizeList>
                        }
                    </AutoSizer>
                </Box>
            </React.Fragment>
        }
    </Box>
}