import apiRequest from "../api/apiRequest";
import projectSource from "../api/projectSource";
import serviceConnectionDetail from "../api/serviceConnectionDetail";
import sourceDataset from "../api/sourceDataset";
import {BaseApiClient} from "./BaseApiClient";

export class SourceApiClient extends BaseApiClient {
    constructor() {
        super("source");
    }

    async TestConnection(data: serviceConnectionDetail) {
        const payload: apiRequest<serviceConnectionDetail> = {data: data};
        const result = await this._post<boolean, serviceConnectionDetail>("test-connection", payload);
        return result;
    }

    async ListEntities(data: projectSource) {
        const payload: apiRequest<projectSource> = {data: data};
        const result = await this._post<sourceDataset[], projectSource>("list-entities", payload);
        return result;
    }

    async ListDatasets(data: projectSource) {
        const payload: apiRequest<projectSource> = {data: data};
        const result = await this._post<projectSource, projectSource>("list-datasets", payload);
        return result;
    }

    async ListClassifications() {
        const result = await this._get<string[]>("list-classifications");
        return result;
    }

    async ListLoadTypes() {
        const result = await this._get<string[]>("list-load-types");
        return result;
    }
}