import {FormControl, FormLabel, Grid, Input} from "@mui/joy";
import React, {FC} from "react";
import {serviceType} from "../../../../api/enums/serviceType";
import serviceConnectionDetail from "../../../../api/serviceConnectionDetail";
import {ServiceConnectionFieldProps} from "./ServiceConnectionFieldProps";

export const ServiceConnectionContainerNameField: FC<ServiceConnectionFieldProps> = (props: ServiceConnectionFieldProps) => <Grid xs={12}>
    <FormControl required={true}>
        <FormLabel>{GetLabelForServiceType(props.existing!.serviceType)}</FormLabel>
        <Input fullWidth={true}
               name={"container"}
               value={props.existing?.containerName}
               onChange={(x) => props.updateConnectionDetails({
                   ...props.existing, containerName: x.target.value
               } as serviceConnectionDetail)}/>
    </FormControl>
</Grid>;

const GetLabelForServiceType = (s: serviceType): string => {
    switch (s) {
        case serviceType.DATABASE_MICROSOFT_SQL_SERVER:
        case serviceType.DATABASE_AZURE_SQL:
        case serviceType.DATABASE_AZURE_SYNAPSE_SQL:
        case serviceType.DATABASE_ORACLE_SQL:
            return "Database";
            break;
        case serviceType.FILE_AZURE_BLOB:
            return "Container";
            break;
        case serviceType.FILE_S3:
            return "Bucket";
            break;
        case serviceType.FILE_AZURE_FILE:
        case serviceType.FILE_SMB:
            return "Share";
            break;
        default:
            return "error";
    }
};