import * as React from "react";
import {FC, useMemo, useState} from "react";
import {useRecoilState} from "recoil";
import projectSource from "../../api/projectSource";
import {CurrentProjectAtom} from "../../atoms/project-atoms/CurrentProjectAtom";
import {
    Divider,
    FormControl,
    Input,
    List,
    ListItem,
    ListItemButton,
    ListItemContent,
    ListItemDecorator,
    ListSubheader,
    Stack,
    Typography
} from "@mui/joy";
import {Collapse} from "@mui/material";
import {ExpandLess, ExpandMore} from "@mui/icons-material";
import {GetDatasetParentLocation} from "../../api-extensions/SourceDatasetExtensions";
import {EntityToOpenInEditorAtom} from "../../atoms/EntityToOpenInEditorAtom";
import { AlertUnsavedChangesDialog, AlertUnsavedChangesDialogProps } from "../Dialogs/AlertUnsavedChangesDialog";
import { EditorEntityChangesSavedAtom as EditorEntityChangesSavedAtom1 } from "../../atoms/EditorEntityChangesSavedAtom";
import { IsAlertUnsavedChangesDialogOpenAtom } from "../../atoms/ModalAtoms";

export const DatasetsPage: FC = () => {
    const [searchText, setSearchText] = useState("");
    const [project,] = useRecoilState(CurrentProjectAtom);
    const [, setEntityToOpenInEditor] = useRecoilState(EntityToOpenInEditorAtom);
    const [changesSaved, setChangesSaved] = useRecoilState(EditorEntityChangesSavedAtom1);

    const [selectedKey, setSelectedKey] = useState<string>("");

    const displayedSources: projectSource[] = useMemo(() =>
    {
        return project?.connections?.filter(x => x.datasets?.length > 0).sort((pa, pb) => pa.sourceName.localeCompare(pb.sourceName)).map(x =>
        {
            return {
                ...x,
                datasets: (x.datasets?.filter(y => y.name.toLowerCase().includes(searchText.toLowerCase())) ?? []).sort((a, b) => a.name.localeCompare(b.name))
            }
        }) ?? [];
    }, [project, searchText]);

    const [isExpanded, setIsExpanded] = useState<string[]>([]);

    const [dialogProps, setDialogProps] = useState<AlertUnsavedChangesDialogProps>({ newDatasetName: "", continueOpen: () => {} });
    const [alertUnsavedChangesDialogOpen, setAlertUnsavedChangesDialogOpen] = useRecoilState(IsAlertUnsavedChangesDialogOpenAtom);


    const handleClick = (selectedKey: string, sourceName: string, datasetName: string) => {
        const contOpen = () => {
            setSelectedKey(selectedKey);
            setEntityToOpenInEditor({
                sourceName: sourceName,
                datasetName: datasetName
            });
        }
        if (!changesSaved) {
            setAlertUnsavedChangesDialogOpen(true);
            setDialogProps({ newDatasetName: datasetName, continueOpen: contOpen});
        } else {
            contOpen();
        }

    }

    return (<React.Fragment>
        <Stack display={"flex"} height={"92vh"} flexGrow={0}>
                     <FormControl>
                         <Input fullWidth placeholder={"Search..."} value={searchText}
                                onChange={(e) => setSearchText(e.target.value)}/>
            </FormControl>
            <List sx={{ overflowY: "auto", height:"100%" }}>
                         {displayedSources?.map((source) => {
                    return <ListItem nested key={source.sourceName + '.li'}>
                               <ListSubheader sticky key={source.sourceName + '.subheader'}>
                                   <Typography level={"body1"}>{source.sourceName}</Typography>
                               </ListSubheader>
                               <Divider/>
                               <List>
                                   {Array.from(new Set(source.datasets.map(ds => GetDatasetParentLocation(ds)))).map((schema) => {
                                const k = source.sourceName + '.' + schema + '.li';
                                return <ListItem nested key={k}>
                                           <ListSubheader sticky key={k + '.subheader'}>
                                               <ListItemContent>
                                                   {schema}
                                               </ListItemContent>
                                               <ListItemDecorator>
                                                   <ListItemButton onClick={() => {
                                                if (isExpanded.includes(k)) {
                                                    setIsExpanded(isExpanded.filter(x => x !== k));
                                                } else {
                                                    setIsExpanded([...isExpanded, k]);
                                                }
                                            }}>{isExpanded.includes(k)
                                                ? <ExpandMore/>
                                                : <ExpandLess/>}
                                                   </ListItemButton>
                                               </ListItemDecorator>
                                           </ListSubheader>
                                           <Collapse in={!isExpanded.includes(k)}>
                                               <List>
                                                   {source.datasets.filter(ds => GetDatasetParentLocation(ds) === schema).map((fds) => {
                                                const k = source.sourceName + '.' + schema + '.' + fds.name + '.libtn';
                                                return <ListItem
                                                    key={k}>
                                                           <ListItemButton
                                                        sx={{ borderRadius: 2 }}
                                                        variant={selectedKey === k ? "solid" : "plain"}
                                                        selected={selectedKey === k}
                                                        onClick={() => handleClick(k, source.sourceName, fds.name)}>
                                                        <ListItemContent>{fds.name.split('/').at(-1)}</ListItemContent>
                                                    </ListItemButton>
                                                </ListItem>
                                            })}
                                               </List>
                                           </Collapse>
                                       </ListItem>
                            })}
                     </List>
                     <ListItem/>
                     </ListItem>
                     })}
                     </List>
                 </Stack>
                 <AlertUnsavedChangesDialog continueOpen={dialogProps.continueOpen} newDatasetName={dialogProps.newDatasetName}/>
             </React.Fragment>
    )
}
