import Button, {ButtonProps} from "@mui/joy/Button";
import {Accordion, AccordionProps, Checkbox, CheckboxProps, TableContainer, TableContainerProps} from "@mui/material";
import {styled} from "@mui/material/styles";
// import IconButton, { IconButtonProps } from '@mui/joy/IconButton';

// Button
export const StyledButton = styled(Button)<ButtonProps>(({theme}) => ({
    // Primary
    "&.JoyButton-colorPrimary": {
        // Solid
        "&.JoyButton-variantSolid": {
            color: "#ffffff", backgroundColor: "#5E4AE3", "&:hover": {
                backgroundColor: "#6e5ce6",
            }, "&:active": {
                backgroundColor: "#5543cc",
            }, "&[disabled]": {
                backgroundColor: "rgba(0, 0, 0, 0.26)"
            },
        }, // Outlined
        "&.JoyButton-variantOutlined": {
            color: "#5E4AE3", borderColor: "#5E4AE3", "&:hover": {
                color: "#5543cc", backgroundColor: "#cfc9f7",
            }, "&:active": {
                color: "#6e5ce6", backgroundColor: "#afa5f1",
            }, "&[disabled]": {
                color: "rgba(0, 0, 0, 0.26)", backgroundColor: "rgba(0, 0, 0, 0.16)"
            },
        }, // Plain
        "&.JoyButton-variantPlain": {
            color: "#5E4AE3", "&:hover": {
                color: "#5543cc", backgroundColor: "#cfc9f7",
            }, "&:active": {
                color: "#6e5ce6", backgroundColor: "#afa5f1",
            }, "&[disabled]": {
                // opacity: .5
                backgroundColor: "rgba(0, 0, 0, 0.26)"
            },
        }, // Soft
        "&.JoyButton-variantSoft": {
            color: "#5E4AE3", backgroundColor: "#efedfc", "&:hover": {
                color: "#5543cc", backgroundColor: "#cfc9f7",
            }, "&:active": {
                color: "#6e5ce6", backgroundColor: "#afa5f1",
            },
        }, "&[disabled]": {
            // opacity: .5
            backgroundColor: "rgba(0, 0, 0, 0.26)"
        },
    }, // button + button
    "& + .JoyButton-root": {
        marginLeft: "8px"
    }, // button + button [sm]
    "& + .JoyButton-sizeSm": {
        marginLeft: "4px"
    },
}));

// Accordion
export const StyledAccordion = styled(Accordion)<AccordionProps>(({theme}) => ({
    // Root
    "&.MuiAccordion-root": {
        // boxShadow: '0.3px 0.8px 1.1px rgba(187 187 187) / 0.11), 0.5px 1.3px 1.8px -0.6px rgba(187 187 187) / 0.18),
        // 1.1px 2.7px 3.8px -1.2px rgba(187 187 187) / 0.26)'
        boxShadow: "none",
        borderBottom: "1px solid #dddee0",
        borderTop: "1px solid #dddee0",

        // Accordion Header
        "& .MuiAccordionSummary-root": {
            minHeight: "48px",
        }, "& .MuiAccordionSummary-content.Mui-expanded": {
            margin: "16px 0"
        },

        // Accordion inside Parent Accordion
        "& .MuiAccordion-root": {
            backgroundColor: "#ffffff",
        },

        // Accordion list items
        "& .JoyListItem-root": {
            display: "flex", justifyContent: "space-between", minWidth: "250px"
        },
    },
}));

// Table
export const StyledTableContainer = styled(TableContainer)<TableContainerProps>(({theme}) => ({
    // Root
    "&.MuiTableContainer-root": {
        padding: "16px", width: "auto", borderRadius: "16px", border: "1px solid #dddee0"
    },
}));

// Checkbox
export const StyledCheckbox = styled(Checkbox)<CheckboxProps>(({theme}) => ({
    // Root
    "&.MuiCheckbox-root": {
        color: "#6e5ce6",

        ".MuiSvgIcon-root": {
            fontSize: "28px"
        },

        "&.Mui-disabled": {
            color: "rgba(0, 0, 0, 0.26)"
        },

    },
}));

// StyledStorageConnectForm
export const StyledStorageConnectForm = styled(`div`)(({theme}) => ({
    paddingBottom: "24px", "& .JoyFormControl-root": {
        marginBottom: "16px"
    }, "& .JoyFormLabel-root": {
        color: "#ffffff"
    }, "& .MuiFormLabel-root": {
        color: "#ffffff"
    }, "& .JoyFormHelperText-root": {
        color: "#ffffff"
    }, "& .MuiTypography-body1": {
        color: "#ffffff"
    }
}));