import {Box} from "@mui/joy";
import {Snackbar} from "@mui/material";
import {FC, ReactElement, ReactNode} from "react";
import {atom, useRecoilState} from "recoil";

export const AlertSnackbarAtom = atom<boolean>({key:'AlertSnackbarAtom', default:false});
export const AlertSnackbarContentAtom = atom<ReactElement>({key:'AlertSnackbarContentAtom', default:<Box/>});

export const AlertSnackbar: FC = () => {
	const [open, setOpen] = useRecoilState(AlertSnackbarAtom);
	const [content, setContent] = useRecoilState(AlertSnackbarContentAtom);

	const handleClose = () => {
		setContent(<Box/>);
		setOpen(false);
	}

	return <Snackbar
		anchorOrigin={{horizontal:"center", vertical:"top"}}
		key={"alertsnackbar"}
		open={open}
		autoHideDuration={5000}
		onClose={handleClose}>
			{content}
	</Snackbar>
}